import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { GetCreateNewEmailTemplateRequest, GetCreateNewEmailTemplateResponse, ICreateNewEmailTemplateService, PostCreateNewEmailTemplateRequest, PostCreateNewEmailTemplateRequestForm } from '../../@intermediate/create-new-email-template';
import { GetEmailTemplateResponse, ModuleTypeTemplate } from '../../@intermediate/email-template';
import { EmailTemplateComponent } from '../../components/email-template/email-template.component';


@Component({
  selector: 'tei-automated-notification-pages-create-new-email-template',
  templateUrl: './create-new-email-template.component.html',
  styleUrls: ['./create-new-email-template.component.scss']
})
export class CreateNewEmailTemplateComponent implements OnInit
{
  isLoading: boolean = false;
  model: GetCreateNewEmailTemplateResponse;
  form: PostCreateNewEmailTemplateRequestForm;
  templates: ModuleTypeTemplate[];
  automationID: number;
  automationType: number;
  categoryID: number;
  emailTemplateRes: GetEmailTemplateResponse;
  @ViewChild(EmailTemplateComponent, { static: true }) child: EmailTemplateComponent;

  constructor(
    @Inject('ICreateNewEmailTemplateService') private CreateNewEmailTemplateService: ICreateNewEmailTemplateService,
    private router: Router,
    activatedRoute: ActivatedRoute,
    private translate: TranslateService,
    private snackbarService: SnackBarService)
  {
    this.form = new PostCreateNewEmailTemplateRequestForm();

    activatedRoute.params.subscribe(params =>
    {

      if (params['automation-id'] && params['automation-type-id'])
      {
        this.automationID = params['automation-id'];
        this.automationType = params['automation-type-id'];
        this.categoryID = params['category-id'];
      }

    });
  }

  ngOnInit()
  {
    //this.form = new PostCreateNewEmailTemplateRequestForm();
    // const request = new GetCreateNewEmailTemplateRequest();
    // this.load(request);
  }

  private load(request: GetCreateNewEmailTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetCreateNewEmailTemplateResponse();
    }

    this.CreateNewEmailTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(response);
    });
  }

  save(form: PostCreateNewEmailTemplateRequestForm)
  {
    if (this.form.invalid)
    {
      return;
    }
    //let request = new PostCreateNewEmailTemplateRequest();
    let request = this.form.get("EmailTemplate").value as PostCreateNewEmailTemplateRequest;
    this.emailTemplateRes = new GetEmailTemplateResponse();
    this.isLoading = true;
    this.CreateNewEmailTemplateService.Post(request).subscribe(
      {
        next: (response) =>
        {
          this.isLoading = false;
          this.snackbarService.showSuccess(this.translate.instant('AUTOMATED_NOTIFICATION.EMAIL_TEMPLATE.TEMPLATE_SAVED'), '', null, 'left');
          this.child.model = response.EmailTemplate;
          this.form.controls["EmailTemplate"].setValue(response.EmailTemplate);
          this.form.controls["EmailTemplate"].updateValueAndValidity();
        },
        error: () => this.isLoading = false
      });
  }

  back()
  {
    this.router.navigate(['/office/marketing/notifications/automate/manage/', this.automationID, this.automationType]);
  }

}
