import { Component, EventEmitter, Input, OnInit, Output, SkipSelf } from "@angular/core";
import { UntypedFormArray } from "@angular/forms";
import { ErrorStateMatcher } from "@angular/material/core";
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from "@tei/core";
import { DeletedFollowUp, GetFollowUpResponse, PostFollowUpRequestForm, FollowUp, FollowUpInterval } from "../../@intermediate/follow-up";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "tei-follow-up",
  templateUrl: "./follow-up.component.html",
  styleUrls: ["./follow-up.component.scss"],
  providers: [
    ngFormGroupControlValueAccessorProviders(FollowUpComponent),
  ],
})

export class FollowUpComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{

  model: GetFollowUpResponse;
  form: PostFollowUpRequestForm;
  intervals: FollowUpInterval[];
  @Input() type: number;

  constructor(@SkipSelf() protected _errorStateMatcher: ErrorStateMatcher, private teiTextTranslateService: TranslateService)
  {
    super(_errorStateMatcher);
    this.intervals = this.getFollowUpIntervals();
    this.form = new PostFollowUpRequestForm(undefined);

  }


  ngOnInit()
  {

  }

  addNotification()
  {
    if (this.getDisable())
    {
      return false;
    }

    let followupFormArray = this.form.get("FollowUp") as UntypedFormArray;
    let followup = new FollowUp();
    followup.AutomationFollowUpID = -1;
    followup.Interval = "H";
    followup.Value = 24;
    followupFormArray.setValue([followup]);
  }

  removeNotification(index: number, control: any)
  {
    let deletedFollowUp = new DeletedFollowUp();
    deletedFollowUp.AutomationFollowUpID = control.value.AutomationFollowUpID;
    this.form.DeletedFollowUp.setValue([deletedFollowUp]);
    this.form.FollowUp.removeAt(index);
  }


  getDisable()
  {
    this.form.FollowUp.markAsTouched();
    return ((this.form.FollowUp.controls.length === 1));
  }

  getFollowUpIntervals(): FollowUpInterval[]
  {
    let intervals =
      [
        {
          Value: "H", Text: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.HRS')
        },
        {
          Value: "D", Text: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.DAYS')
        },
        {
          Value: "M", Text: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.MINS')
        },
      ];

    return intervals;
  }

}


