import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { BehaviorSubject } from 'rxjs';
import { GetTemplateBuilderRequest, GetTemplateBuilderResponse, ITemplateBuilderService, PostTemplateBuilderRequest, PostTemplateBuilderRequestForm } from '../../@intermediate/template-builder';


@Component({
  selector: 'tei-automated-notification-pages-template-builder',
  templateUrl: './template-builder.component.html',
  styleUrls: ['./template-builder.component.scss']
})
export class TemplateBuilderComponent implements OnInit
{
  isLoading: BehaviorSubject<boolean>;
  model: GetTemplateBuilderResponse;
  form: PostTemplateBuilderRequestForm;
  req: PostTemplateBuilderRequest;
  templateID: string;
  tokens: string[];
  email: string;
  htmlBuilderApiKey: string;
  uploadImagepath: string;

  constructor(
    @Inject('ITemplateBuilderService') private TemplateBuilderService: ITemplateBuilderService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
  )
  {

    this.activatedRoute.params.subscribe(params =>
    {
      if (params['template-id'])
      {
        this.templateID = params['template-id'];
      }
    });
    this.form = new PostTemplateBuilderRequestForm();

  }


  ngOnInit()
  {
    const request = new GetTemplateBuilderRequest();
    request.TemplateID = this.templateID;
    this.load(request);
  }

  private load(request: GetTemplateBuilderRequest)
  {
    if (!this.model)
    {
      this.model = new GetTemplateBuilderResponse();
    }
    let emailTemplate = "";
    this.TemplateBuilderService.Get(request).subscribe(response =>
    {
      this.model = response;
      emailTemplate = response.BodyStructure;
      this.tokens = response.Tokens;
      this.email = response.BodyStructure;
      this.htmlBuilderApiKey = response.ApiKey;
      this.uploadImagepath = response.UploadImagePath;
    });
  }

  save()
  {
    this.TemplateBuilderService.Post(this.req).subscribe(response =>
    {
    });
  }

  templateSaved($event)
  {
    this.req = new PostTemplateBuilderRequest();
    this.req.Body = $event.EmailTemplate;
    this.req.BodyStructure = $event.EmailStructure;
    this.req.TemplateID = this.templateID;
    this.TemplateBuilderService.Post(this.req).subscribe(response =>
    {
      this.snackbarService.showSuccess(this.translate.instant('AUTOMATED_NOTIFICATION.EMAIL_TEMPLATE.TEMPLATE_SAVED'), '', null, 'left');
    });
  }
}
