import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SnackBarService } from '@tei/material';
import { GetCreateNewPortalMessageRequest, GetCreateNewPortalMessageResponse, ICreateNewPortalMessageService, PostCreateNewPortalMessageRequest, PostCreateNewPortalMessageRequestForm } from '../../@intermediate/create-new-portal-message';
import { GetPortalMessageTemplateResponse } from '../../@intermediate/portal-message-template';

@Component({
  selector: 'tei-automated-notification-pages-create-new-portal-message',
  templateUrl: './create-new-portal-message.component.html',
  styleUrls: ['./create-new-portal-message.component.scss']
})
export class CreateNewPortalMessageComponent implements OnInit
{
  model: GetCreateNewPortalMessageResponse;
  form: PostCreateNewPortalMessageRequestForm;
  automationType: number;
  automationName: string;
  automationID: number;
  categoryID: number;
  groupName: string;

  constructor(@Inject('ICreateNewPortalMessageService') private CreateNewPortalMessageService: ICreateNewPortalMessageService,
    activatedRoute: ActivatedRoute,
    private router: Router,
    private chRef: ChangeDetectorRef,
    private snackbarService: SnackBarService
  )
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['automation-type-id'] && params['automation-name'])
      {
        this.automationType = params['automation-type-id'];
        this.automationName = params['automation-name'];
      }

      if (params['automation-id'] && params['automation-type-id'])
      {

        this.automationID = params['automation-id'];
        this.automationType = params['automation-type-id'];
        this.categoryID = params['category-id'];
      }
      this.groupName = params['group-name'];

    });

    this.form = new PostCreateNewPortalMessageRequestForm(null);
  }

  ngOnInit()
  {

  }

  private load(request: GetCreateNewPortalMessageRequest)
  {
    if (!this.model)
    {
      this.model = new GetCreateNewPortalMessageResponse();
    }

    this.CreateNewPortalMessageService.Get(request).subscribe(response =>
    {
      // this.model = response;
    });
  }

  save(form: PostCreateNewPortalMessageRequestForm)
  {
    let request = new PostCreateNewPortalMessageRequest();
    request.AutomationTypeID = this.automationType;
    request.PortalMessageTemplate = form.get('PortalMessageTemplateControl').value as GetPortalMessageTemplateResponse[];
    request.CategoryID = this.categoryID;
    request.AutomationID = this.automationID;
    let res = new GetCreateNewPortalMessageResponse();
    this.CreateNewPortalMessageService.Post(request).subscribe(response =>
    {
      this.snackbarService.showSuccess('Portal message saved.', '', null, 'left');
      // let portalMessage = new GetPortalMessageTemplateResponse();
      // const c  = new PostPortalMessageTemplateRequestForm(response.PortalMessageTemplate);
      this.form.controls["PortalMessageTemplateControl"].setValue(response.PortalMessageTemplate);
      this.form.controls["PortalMessageTemplateControl"].updateValueAndValidity();
    });

  }

  back()
  {
    this.router.navigate(['/office/marketing/notifications/automate/manage', this.automationID, this.automationType]);
  }
}
