import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatCardModule } from '@angular/material/card';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { NotificationsComponent } from './notifications.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, SecureNavigationModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [NotificationsComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: NotificationsComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  providers: [],
})
export class NotificationsModule { }
