import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ChipOptionModel, ChipType } from '@tei/material';
import { concat, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { GetCampaignsRequest } from '../../@intermediate/campaigns';

@Component({
  selector: 'tei-automated-notification-campaigns-search-header',
  templateUrl: './campaigns-search-header.component.html',
  styleUrls: ['./campaigns-search-header.component.scss']
})
export class CampaignsSearchHeaderComponent implements OnInit
{
  ChipType = ChipType;
  @Output() enterFilterValueEvent = new EventEmitter<GetCampaignsRequest>();
  form: UntypedFormGroup;

  dateRange: ChipOptionModel;

  filterTypeaheadInput = new Array();
  filterChipArray: Observable<any[]> = of([]);
  activeChipDisplayName = '';
  chipItemsWithDisplayName = [{ chipName: 'Status', displayName: 'Status' }];
  selectedChipValues = new Map<string, string>();
  statuses = [
    {
      Status: 'Drafts',
    }, {
      Status: 'Sent',
    }, {
      Status: 'Scheduled',
    }, {
      Status: 'Cancelled',
    },
  ];

  constructor()
  {
    this.chipItemsWithDisplayName.forEach(el =>
    {
      this.filterTypeaheadInput.push(new Subject<string>());
    });
  }

  ngOnInit(): void
  {
    this.form = new UntypedFormGroup({
      filter: new UntypedFormControl()
    });
  }

  chipItem(event: { chipName: string, index: number; })
  {
    this.activeChipDisplayName = event.chipName;
  }

  onFilterChange(filterObject): void
  {
  }

  filterByChipItem(item: { chipName: string, index: number; })
  {
    this.chipItem(item);
    this.filterChipArray[item.index] = concat(
      of([]), // default items
      this.filterTypeaheadInput[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getfilterChipData(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  getfilterChipData(term: string = null, item: string): Observable<Array<any>>
  {
    let items = this.statuses;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item].toLocaleLowerCase().indexOf(lowerTerm) > -1
      );
    }
    return of(items).pipe(delay(200));
  }

  chipItemValues(selectedValues: string)
  {
    this.selectedChipValues.set(this.activeChipDisplayName, selectedValues);
    this.load();
  }

  removeItem(event: { chipName: string; })
  {
    this.selectedChipValues.delete(event.chipName);
    this.load();
  }

  onRangeItemChangeEvent(chipOptionModel: ChipOptionModel): void
  {
    this.dateRange = chipOptionModel;
    this.load();
  }

  enterFilterValue($event: Event): void
  {
    $event?.stopPropagation();
    $event?.preventDefault();
    setTimeout(() => { this.load(); }, 300);
  }

  public load()
  {
    const request = new GetCampaignsRequest();
    this.selectedChipValues.forEach((value, key, map) =>
    {
      request[key] = value;
    });

    const filter = this.form.get('filter').value;
    if (filter)
    {
      request.Filter = filter;
    }

    this.enterFilterValueEvent.emit(request);
  }
}

