import { Component } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';

@Component({
  selector: 'tei-automated-sent-notification-content-dialog',
  templateUrl: './sent-notification-content-dialog.component.html',
  styleUrls: ['./sent-notification-content-dialog.component.scss']
})
export class SentNotificationContentDialogComponent
{

  content = '';
  constructor(
    public dialogRef: MatDialogRef<SentNotificationContentDialogComponent>,
  ) {}

  closeDialog()
  {
    this.dialogRef.close();
  }
}
