import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetEditEmailTemplateRequest, GetEditEmailTemplateResponse, IEditEmailTemplateService, PostEditEmailTemplateRequest, PostEditEmailTemplateResponse } from '../@intermediate/edit-email-template';

@Injectable({
  providedIn: 'root'
})

export class EditEmailTemplateService implements IEditEmailTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/EditEmailTemplate';
  }

  public Get(request: GetEditEmailTemplateRequest): Observable<GetEditEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetEditEmailTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostEditEmailTemplateRequest): Observable<PostEditEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostEditEmailTemplateResponse>(apiUrl, request);
  }
}
