import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCreateNewAutomationTypeRequest, GetCreateNewAutomationTypeResponse, ICreateNewAutomationTypeService, PostCreateNewAutomationTypeRequest, PostCreateNewAutomationTypeResponse } from '../@intermediate/create-new-automation-type';

@Injectable({
  providedIn: 'root'
})

export class CreateNewAutomationTypeService implements ICreateNewAutomationTypeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/CreateNewAutomationType';
  }

  public Get(request: GetCreateNewAutomationTypeRequest): Observable<GetCreateNewAutomationTypeResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCreateNewAutomationTypeResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCreateNewAutomationTypeRequest): Observable<PostCreateNewAutomationTypeResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateNewAutomationTypeResponse>(apiUrl, request);
  }
}
