import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogModule as MatDialogModule, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MaterialGenericFilterModule } from '@tei/material-generic-filter';
import { DefaultTabLayoutModule } from '@tei/theme';
import { TemplatesFiltersDialogService } from '../../@services/templates-filters-dialog.service';
import { TemplatesFiltersDialogComponent } from './templates-filters-dialog.component';

const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MaterialGenericFilterModule, MatCardModule, MatListModule, MatTableModule, MatInputModule, MatPaginatorModule, MatFormFieldModule, MatIconModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [TemplatesFiltersDialogComponent];

@NgModule({
    declarations: [...COMPONENTS],
    imports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        DefaultTabLayoutModule
    ],
    exports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...COMPONENTS
    ],
    providers: [{ provide: 'ITemplatesFiltersDialogService', useClass: TemplatesFiltersDialogService },
    { provide: MatDialogRef, useValue: {} }, { provide: MAT_DIALOG_DATA, useValue: [] },
    ]
})
export class TemplatesFiltersDialogModule { }
