import { Inject, Injectable } from '@angular/core';
//import { Subscription } from 'rxjs';
import { UntypedFormGroup} from '@angular/forms';
import { BehaviorSubject } from 'rxjs';
import { Observable } from 'rxjs';
import { AutomationModule } from '../@intermediate/templates';

@Injectable({
  providedIn: 'root'
})
export class TemplateFilterService {
  private name: UntypedFormGroup;

  constructor() {}
  public getFilters: BehaviorSubject<Filter> = new BehaviorSubject<Filter>(null);

  currentFilter = this.getFilters.asObservable();
  public setValue(value: Filter): void
  {
    this.getFilters.next(value);
  }

  }

export class Filter
 {
  public SearchString: string ;
  public AutomationModule: AutomationModule[];
 
 }
