import { ChangeDetectorRef, Component, ElementRef, Inject, Input, OnInit, SkipSelf, ViewChild } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetAutomateResponse } from '../../@intermediate/automate';
import { GetCreateNewAutomationTypeRequest, GetCreateNewAutomationTypeResponse, ICreateNewAutomationTypeService, PostCreateNewAutomationTypeRequestForm } from '../../@intermediate/create-new-automation-type';



@Component({
  selector: 'tei-automated-notification-create-new-automation-type',
  templateUrl: './create-new-automation-type.component.html',
  styleUrls: ['./create-new-automation-type.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(CreateNewAutomationTypeComponent)
  ]
})
export class CreateNewAutomationTypeComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetCreateNewAutomationTypeResponse;
  form: PostCreateNewAutomationTypeRequestForm;
   @Input() autoMateTypes: GetAutomateResponse ;  
   @Input() automateLabelName: string;  
   automateModules: GetAutomateResponse ;  
   @ViewChild('automateFocus') automateFocus: ElementRef;
   
  constructor(
    private cd : ChangeDetectorRef,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('ICreateNewAutomationTypeService') private CreateNewAutomationTypeService: ICreateNewAutomationTypeService
    )
    
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostCreateNewAutomationTypeRequestForm();    
    this.automateModules = new  GetAutomateResponse();    
    this.automateModules.AutomatedModuleFilters = this.autoMateTypes.AutomatedModuleFilters.slice().sort((a, b) => a.ModuleName.localeCompare(b.ModuleName));
    this.automateModules.Automations= this.autoMateTypes.Automations;
    
  }

  ngAfterViewInit(): void {
    
  //  this.automateFocus.nativeElement.focus();
   // this.cd.detectChanges();
  }
  

  private load(request: GetCreateNewAutomationTypeRequest)
  {
    if (!this.model)
    {
      this.model = new GetCreateNewAutomationTypeResponse();
    }

    this.CreateNewAutomationTypeService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.writeValue(this.model);
    });
  }
}
