import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTemplatesRequest, GetTemplatesResponse, ITemplatesService, PostTemplatesRequest, PostTemplatesResponse } from '../@intermediate/templates';
import { DeleteTemplatesRequest, DeleteTemplatesResponse } from '../@intermediate/templates/models/delete-templates.model';
import { DuplicateTemplatesRequest, DuplicateTemplatesResponse } from '../@intermediate/templates/models/duplicate-templates.model';

@Injectable({
  providedIn: 'root'
})

export class TemplatesService implements ITemplatesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Templates';
  }

  public Get(request: GetTemplatesRequest): Observable<GetTemplatesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetTemplatesResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTemplatesRequest): Observable<PostTemplatesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostTemplatesResponse>(apiUrl, request);
  }

  public Delete(request: DeleteTemplatesRequest): Observable<DeleteTemplatesResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.delete<DeleteTemplatesResponse>(apiUrl, { params: request as any });
  }

  public Duplicate(request: DuplicateTemplatesRequest): Observable<DuplicateTemplatesResponse>
  {
    const apiUrl = this.serviceUrl + "/Duplicate";
    return this.http.post<DuplicateTemplatesResponse>(apiUrl, request);
  }
}
