import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SnackBarModule } from '@tei/material';
import { CreateNewPortalMessageService } from '../../@services/create-new-portal-message.service';
import { PortalMessageTemplateModule } from '../../components/portal-message-template/portal-message-template.module';
import { CreateNewPortalMessageComponent } from './create-new-portal-message.component';



const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [];
const TEI_MATERIAL_MODULES = [PortalMessageTemplateModule, SnackBarModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [CreateNewPortalMessageComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: CreateNewPortalMessageComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],

  providers: [{ provide: 'ICreateNewPortalMessageService', useClass: CreateNewPortalMessageService }],
})
export class CreateNewPortalMessageModule { }
