import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetCreateNewPortalMessageResponse } from './get-create-new-portal-message.model';
import { GetPortalMessageTemplateResponse, PostPortalMessageTemplateRequestFormArray } from '../../portal-message-template';



export class PostCreateNewPortalMessageRequest
{
  PortalMessageTemplate: GetPortalMessageTemplateResponse[];
  AutomationTypeID: number;
  CategoryID: number;
  AutomationID: number;
}

export class PostCreateNewPortalMessageRequestForm extends UntypedFormGroup
{
  

  constructor(automationResponse:GetCreateNewPortalMessageResponse)
  {
    super({
      PortalMessageTemplate: new PostPortalMessageTemplateRequestFormArray(automationResponse),
      PortalMessageTemplateControl: new UntypedFormControl(),
     
    });
    
  }

  public value: PostCreateNewPortalMessageRequest;

  public setValue(value: GetCreateNewPortalMessageResponse)
  {
    const request = new PostCreateNewPortalMessageRequest();

    request.PortalMessageTemplate=value.PortalMessageTemplate;
    
    super.setValue(request);
  }
}

export class PostCreateNewPortalMessageResponse
{
  AutomationID: number;
  AutomationType: number;
  AutomationCategoryMessageID: number;
  FromUserID: number;
  PortalMessageTemplate: GetPortalMessageTemplateResponse;
}
