import { AutomatedNotificationToken } from '../../portal-message-template';

export class GetEmailTemplateRequest
{
  public CategoryID: number;
  public AutomationID: number;
  public AutomationType: number;
}

export class GetEmailTemplateResponse
{
  AutomationName: string;
  Description: string;
  Title: string;
  FromName: string;
  FromEmail: string;
  ConfirmationAddresses: string;       
  TemplateID: string;
  CategoryID: number;
  AutomationID: number;
  IsCustomized: boolean;
  Templates: ModuleTypeTemplate[];
  AutomationCategoryTemplateID: string;
  Subject: string;
  Body: string;
  Tokens: AutomatedNotificationToken[];
}



export class ModuleTypeTemplate
{
    TemplateID: string;
    TemplateName: string;
}