import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAutomationMatDialogRequest, GetAutomationMatDialogResponse, IAutomationMatDialogService } from '../@intermediate/automation-mat-dialog';

@Injectable({
  providedIn: 'root'
})

export class AutomationMatDialogService implements IAutomationMatDialogService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/AutomationMatDialog';
  }

  public Get(request: GetAutomationMatDialogRequest): Observable<GetAutomationMatDialogResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAutomationMatDialogResponse>(apiUrl, { params: request as any });
  }
}
