import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
//import { User } from '@tei/general-shared';
import { NotificationType } from '../../create-new-automation/models/get-create-new-automation.model';
import { GetCreateNewPortalMessageResponse } from '../../create-new-portal-message';
import { User } from '../../user.model';
import { AutomatedNotificationToken, GetPortalMessageTemplateResponse, UserProfile } from './get-portal-message-template.model';


export class PostPortalMessageTemplateRequest
{
  public AutomationCategoryMessageID?: number;
  public CategoryID: number;
  public AutomationID: number;
  public User: User;
  public MessageContent: string;
  public Tokens: AutomatedNotificationToken[];
  public Title: string;
  public Subtitle: string;
  public IsEnabled: boolean;
  public UserProfile: UserProfile;

}

export class PostPortalMessageTemplateRequestForm extends UntypedFormGroup
{
  constructor(portalMessage: GetPortalMessageTemplateResponse)
  {
    super({
      AutomationCategoryMessageID: new UntypedFormControl(portalMessage.AutomationCategoryMessageID != undefined ? portalMessage.AutomationCategoryMessageID : null),
      CategoryID: new UntypedFormControl(portalMessage.CategoryID != undefined ? portalMessage.CategoryID : null),
      AutomationID: new UntypedFormControl(''),
      User: new UntypedFormControl(portalMessage.User != undefined ? portalMessage.User : null),
      MessageContent: new UntypedFormControl(portalMessage.MessageContent != undefined ? portalMessage.MessageContent : null),
      Tokens: new UntypedFormControl(''),
      Title: new UntypedFormControl(portalMessage.Title != undefined ? portalMessage.Title : ""),
      SubTitle: new UntypedFormControl(portalMessage.SubTitle != undefined ? portalMessage.SubTitle : ""),
      IsEnabled: new UntypedFormControl(portalMessage.IsEnabled != undefined ? portalMessage.IsEnabled : ""),
      UserProfile: new UntypedFormControl(portalMessage.UserProfile != undefined ? portalMessage.UserProfile : ""),
    });

  }

  public value: PostPortalMessageTemplateRequest;

  public setValue(
    value: GetPortalMessageTemplateResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new GetPortalMessageTemplateResponse();

    request.AutomationCategoryMessageID = value.AutomationCategoryMessageID;
    request.CategoryID = value.CategoryID;
    request.AutomationID = value.AutomationID;
    request.User = value.User;
    request.MessageContent = value.MessageContent;
    request.Tokens = value.Tokens;
    request.Title = value.Title;
    request.SubTitle = value.SubTitle;
    request.IsEnabled = value.IsEnabled;
    request.UserProfile = value.UserProfile;
    super.setValue(request, options);
  }
}


export class PostPortalMessageTemplateRequestFormArray extends UntypedFormArray
{
  portalMessageNotification: NotificationType[];
  portalMessageTemplate: GetPortalMessageTemplateResponse[];

  constructor(portalMessage: GetCreateNewPortalMessageResponse)
  {
    super([]);
    if (portalMessage != null)
    {
      const postPortalMessageForm = new GetPortalMessageTemplateResponse();


      if (portalMessage.PortalMessageTemplate != null)
      {

        portalMessage.PortalMessageTemplate.forEach(message =>
        {
          postPortalMessageForm.MessageContent = message.MessageContent;
          postPortalMessageForm.AutomationCategoryMessageID = message.AutomationCategoryMessageID;
          postPortalMessageForm.User = message.User;
          postPortalMessageForm.AutomationID = message.AutomationID;
          this.setValue([postPortalMessageForm]);
        });


      }
    }
  }






  public setValue(
    portalMessages: GetPortalMessageTemplateResponse[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (portalMessages)
    {
      portalMessages.forEach(portalMessage =>
      {
        const postPortalMessageFormControl = new PostPortalMessageTemplateRequestForm(portalMessage);
        this.push(postPortalMessageFormControl);
      });
    }

  }
}


export class PostPortalMessageTemplateResponse
{

}
