import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';

export class GetTemplatesRequest
{

}

export class GetTemplatesResponse
{
    Templates: Template[];
    TemplateCategories: TemplateCategory[];
    AutomationModules: AutomationModule[];
}

export class Template
{
    TemplateID: string;
    ModuleID: number;
    AutomationModuleTypeID: number;
    TemplateCategoryID: number;
    Name: string;
    Type: string;
    Module: string;
    Description: string;
    Body: string;
    Subject: string;
    Selected: boolean;
    InUse: boolean;
}

export class TemplateCategory
{
    TemplateCategoryName: string;
    Category: number;
    IsEnabled: boolean;
}

export class AutomationModule
{
    ModuleID: number;
    ModuleName: string;
    IsEnabled: boolean;
}

export class TemplateMetadata
{
  public readonly Columns: string[] = ['Name', 'Module','Type','Description','actions'];
}

export class GetAutomateRequestForm extends UntypedFormGroup
{
    constructor()
    {
        super({
            SearchString: new UntypedFormControl(''),
        });
    }

}





