import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTemplateBuilderRequest, GetTemplateBuilderResponse, ITemplateBuilderService, PostTemplateBuilderRequest, PostTemplateBuilderResponse } from '../@intermediate/template-builder';

@Injectable({
  providedIn: 'root'
})

export class TemplateBuilderService implements ITemplateBuilderService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/TemplateBuilder';
  }

  public Get(request: GetTemplateBuilderRequest): Observable<GetTemplateBuilderResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetTemplateBuilderResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTemplateBuilderRequest): Observable<PostTemplateBuilderResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostTemplateBuilderResponse>(apiUrl, request);
  }
}
