import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { MatToolbarModule } from '@angular/material/toolbar';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AutomationMatDialogService } from '../../@services/automation-mat-dialog.service';
import { TemplatesFiltersDialogService } from '../../@services/templates-filters-dialog.service';
import { CreateNewAutomationTypeModule } from '../../components/create-new-automation-type/create-new-automation-type.module';
import { AutomationMatDialogComponent } from './automation-mat-dialog.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule,];
const MATERIAL_MODULES = [MatTabsModule, MatToolbarModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [CreateNewAutomationTypeModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

@NgModule({
  declarations: [AutomationMatDialogComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],

  providers: [
    { provide: 'IAutomationMatDialogService', useClass: AutomationMatDialogService },
    { provide: 'ITemplatesFiltersDialogService', useClass: TemplatesFiltersDialogService }
  ],
})
export class AutomationMatDialogModule
{

}
