import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatSortModule } from '@angular/material/sort';
import { RouterModule } from '@angular/router';
import { ResponsiveTableHeightModule } from '@tei/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, ConfirmDialogModule, FilterChipsModule, QuickSearchFilterChipModule } from '@tei/material';
import { DefaultTabLayoutModule } from '@tei/theme';
import { AutomateService } from '../../@services/automate.service';
import { AutomateSearchHeaderComponent } from '../../components/automate-search-header/automate-search-header.component';
import { CreateNewAutomationTypeModule } from '../../components/create-new-automation-type/create-new-automation-type.module';
import { TemplatesFiltersDialogModule } from '../../components/templates-filters-dialog/templates-filters-dialog.module';
import { AutomationMatDialogModule } from '../automation-mat-dialog/automation-mat-dialog.module';
import { AutomateComponent } from './automate.component';


const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSortModule, MatDialogModule, MatCheckboxModule, MatCardModule, MatListModule, MatTableModule, MatPaginatorModule, MatButtonModule, MatIconModule, MatMenuModule, MatDividerModule];
const TEI_MATERIAL_MODULES = [AutomationMatDialogModule, CreateNewAutomationTypeModule, ConfirmDialogModule, TemplatesFiltersDialogModule, BreadCrumbModule, QuickSearchFilterChipModule, FilterChipsModule];
const TEI_CORE_MODULES = [ResponsiveTableHeightModule, TeiTextTranslatePipeModule];
const COMPONENTS = [AutomateComponent, AutomateSearchHeaderComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    RouterModule.forChild([{ path: '', component: AutomateComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    DefaultTabLayoutModule,
  ],
  providers: [{ provide: 'IAutomateService', useClass: AutomateService }]
})
export class AutomateModule { }
