import { ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { MatLegacySelectionListChange as MatSelectionListChange } from '@angular/material/legacy-list';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { GetCreateNewAutomationRequest, GetCreateNewAutomationResponse, ICreateNewAutomationService, NotificationType, PostCreateNewAutomationRequestForm } from '../../@intermediate/create-new-automation';
import { PostFollowUpRequestForm } from '../../@intermediate/follow-up';
import { PostReminderRequestForm } from '../../@intermediate/reminder';


@Component({
  selector: 'tei-automated-notification-pages-createnewautomation',
  templateUrl: './create-new-automation.component.html',
  styleUrls: ['./create-new-automation.component.scss']
})
export class CreateNewAutomationComponent implements OnInit
{
  model: GetCreateNewAutomationResponse;
  form: PostCreateNewAutomationRequestForm;
  request: GetCreateNewAutomationRequest;
  automationType: number;
  automationName: string;
  automationID: number;
  categoryID: number;
  emailTemplate: NotificationType[];
  portalMessgaeTemplate: NotificationType[];
  pushNotificationTemplate: NotificationType[];
  portalMessageTemplateEnabled: boolean;
  emailTemplateEnabled: boolean;
  emailTemplateRowIndex: number;
  portalMessageTemplateRowIndex: number;
  pushNotificationTemplateEnabled: boolean;
  pushNotificationTemplateRowIndex: number;
  redirectCreate: boolean = false;
  redirectManage: boolean = false;
  isLoading: boolean = false;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.AUTOMATE.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.NOTIFICATIONS.NOTIFICATIONS'),
      relativeUrl: '../../../../'
    },
    {
      title: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.AUTOMATE.AUTOMATIONS'),
      relativeUrl: '../../../'
    },
    {
      title: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.AUTOMATE.EDIT'),
      relativeUrl: ''
    }
  ];

  constructor(
    @Inject('ICreateNewAutomationService') private CreateNewAutomationService: ICreateNewAutomationService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    public cdref: ChangeDetectorRef,
    private snackbarService: SnackBarService,
    private teiTextTranslateService: TranslateService,
  )
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['automation-type-id'] && params['automation-name'])
      {
        // this.form = new PostCreateNewAutomationRequestForm(null,null,null,null);
        this.automationType = params['automation-type-id'];
        this.automationName = params['automation-name'];
        this.redirectCreate = true;
      }

      if (params['automation-id'] && params['automation-type-id'])
      {
        //   this.form = new PostCreateNewAutomationRequestForm(null,null,null,null);
        this.automationID = params['automation-id'];
        this.automationType = params['automation-type-id'];
        this.categoryID = params['category-id'];
        this.redirectManage = true;
      }

    });
  }

  ngOnInit()
  {
    this.form = new PostCreateNewAutomationRequestForm(null, null, null, null, null, null);
    this.request = new GetCreateNewAutomationRequest();
    this.request.AutomationType = this.automationType;
    this.request.AutomationID = this.automationID ? this.automationID : -1;
    this.load(this.request);
  }

  private load(request: GetCreateNewAutomationRequest)
  {
    this.isLoading = true;
    if (!this.model)
    {
      this.model = new GetCreateNewAutomationResponse();
    }

    const automationTypeID = this.automationType;
    const automationName = this.automationName;

    this.CreateNewAutomationService.Get(request).subscribe(response =>
    {
      // this.model = response;
      // this.model.AutomationName = automationName == undefined ? response.AutomationName : automationName;
      // this.model.AutomationType = automationTypeID;
      // this.model.AutomationID = response.AutomationID;
      // this.model.Reminders = response?.Reminders ? response?.Reminders : null;
      // const c = new PostReminderRequestForm(response?.Reminders);
      // this.form.setValue(this.model);
      this.setFormvalue(response);
      this.save(null);
    });
  }

  onChange($event: MatSelectionListChange)
  {
    const selectedItem = $event.options[0].value;
    let catogoryID = selectedItem.CategoryID;
    this.portalMessageTemplateEnabled = true;
    this.emailTemplateEnabled = false;
    this.pushNotificationTemplateEnabled = false;

    this.model.NotificationTypes.forEach(element =>
    {
      if (element.CategoryID == catogoryID && ($event.options[0].selected == false))
      {
        element.IsEnabled = false;
        this.form.controls["NotificationTypes"].patchValue(this.model.NotificationTypes);
      }
      if (element.CategoryID == catogoryID && ($event.options[0].selected == true))
      {
        element.IsEnabled = true;
        this.form.controls["NotificationTypes"].patchValue(this.model.NotificationTypes);
      }
    });
  }

  save(from: PostCreateNewAutomationRequestForm)
  {

    this.updateReminderFormValue();
    this.updateFollowUpFormValue();

    this.CreateNewAutomationService.Post(this.form.value).subscribe(response =>
    {
      if (from)
      {
        this.snackbarService.showSuccess(this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.AUTOMATION_GROUP_SAVED'), '', null, 'left');
      }

      if (response.AutomationID && this.redirectCreate)
      {
        this.automationID = response.AutomationID;
        // this.router.navigate(['/office/marketing/notifications/automate/manage/', response.AutomationID, response.AutomationType], { relativeTo: this.activatedRoute });
      }
      this.setFormvalue(null);
      this.isLoading = false;

    });
  }

  private updateReminderFormValue()
  {
    if (this.form.value.Reminders != null && this.form.value.Reminders["Reminder"])
    {
      this.form.value.Reminders = this.form.controls.Reminders.value["Reminder"];
      this.form.value.DeletedReminders = this.form.controls.Reminders.value["DeletedReminder"];
    }
  }

  private updateFollowUpFormValue()
  {
    if (this.form.value.FollowUps != null && this.form.value.FollowUps["FollowUp"])
    {
      this.form.value.FollowUps = this.form.controls.FollowUps.value["FollowUp"];
      this.form.value.DeletedFollowUps = this.form.controls.FollowUps.value["DeletedFollowUp"];
    }
  }

  setFormvalue(response: GetCreateNewAutomationResponse)
  {
    if (response)
    {
      this.setValue(response);
    }
    else
    {
      this.request = new GetCreateNewAutomationRequest();
      this.request.AutomationType = this.automationType;
      this.request.AutomationID = this.automationID ? this.automationID : -1;
      this.CreateNewAutomationService.Get(this.request).subscribe(response =>
      {
        this.setValue(response);
      });
    }
  }


  private setValue(response: GetCreateNewAutomationResponse)
  {
    this.model = new GetCreateNewAutomationResponse();
    this.form = new PostCreateNewAutomationRequestForm(null, null, null, null, null, null);
    this.model = response;
    this.model.AutomationName = this.automationName == undefined ? response.AutomationName : this.automationName;
    this.model.AutomationType = this.automationType;
    this.model.AutomationID = response.AutomationID;
    this.model.Reminders = response?.Reminders ? response?.Reminders : null;
    this.model.FollowUps = response?.FollowUps ? response?.FollowUps : null;
    const reminder = new PostReminderRequestForm(response?.Reminders);
    const followUp = new PostFollowUpRequestForm(response?.FollowUps);
    this.form.setValue(this.model);
  }

  portalMessage(categoryID: number, groupName: string)
  {
    // if (!this.redirectManage)
    // {
    //   return;
    // }

    if (groupName == "PortalMessage")
    {
      this.router.navigate(['/office/marketing/notifications/create-new-portal-message/manage', this.automationID, this.automationType, categoryID, groupName], { relativeTo: this.activatedRoute });
    }

    if (groupName == "Email")
    {
      this.router.navigate(['/office/marketing/notifications/create-new-email-template', 'manage', this.automationID, this.automationType, categoryID, groupName], { relativeTo: this.activatedRoute });
    }

  }

  back()
  {
    this.router.navigate(['/office/marketing/notifications/automate/manage/', this.automationID, this.automationType], { relativeTo: this.activatedRoute });
  }
}
