import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetTemplateBuilderResponse } from './get-template-builder.model';

export class PostTemplateBuilderRequest
{
  TemplateID: string;
  Body: string;
  BodyStructure: string;
  
}

export class PostTemplateBuilderRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      BodyStructure: new UntypedFormControl(),
    });
  }

  public value: PostTemplateBuilderRequest;

  public setValue(value: GetTemplateBuilderResponse)
  {
    const request = new PostTemplateBuilderRequest();

    request.BodyStructure = value.BodyStructure;

    super.setValue(request);
  }
}

export class PostTemplateBuilderResponse
{

}
