import { User } from "../../user.model";

export class GetPortalMessageTemplateRequest
{
  public AutomationCategoryMessageID?: number;
  public AutomationType?: number;
  public CategoryID?: number;
  public AutomationID?: number;
}

export class GetPortalMessageTemplateResponse
{

  AutomationCategoryMessageID: number;
  CategoryID: number;
  AutomationID: number;
  User: User;
  MessageContent: string;
  Tokens: AutomatedNotificationToken[];
  Title: string;
  PortalMessageTitle: string;
  SubTitle: string;
  IsEnabled: boolean;
  UserProfile: UserProfile;
}

export class AutomatedNotificationToken
{
  Token: string;
  Title: string;
  Description: string;
}


export class UserProfile
{
  FirstName: string;
  LastName: string;
  ProfilePicturePath: string;
}