import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatSortModule } from '@angular/material/sort';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { CardDialogModule, FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { DefaultTabLayoutModule } from '@tei/theme';
import { CampaignsService } from '../../@services/campaigns.service';
import { CampaignsSearchHeaderComponent } from '../../components/campaigns-search-header/campaigns-search-header.component';
import { CampaignsComponent } from './campaigns.component';

const BASE_MODULES = [FormsModule, CommonModule, RouterModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSortModule, MatCardModule, MatTableModule, MatInputModule, MatPaginatorModule, MatMenuModule];
const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, FilterChipsModule, CardDialogModule, LoadingModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [CampaignsComponent, CampaignsSearchHeaderComponent];


@NgModule({
    declarations: [...COMPONENTS],
    imports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...TEI_CORE_MODULES,
        DefaultTabLayoutModule,
    ],
    providers: [{ provide: 'ICampaignsService', useClass: CampaignsService }]
})
export class CampaignsModule { }
