import { UntypedFormGroup, UntypedFormControl, Validators } from '@angular/forms';
import { GetCreateNewTemplateResponse, GeneralModule, AutomationModuleType } from './get-create-new-template.model';

export class PostCreateNewTemplateRequest
{
  TemplateID: string;  
  TemplateCategoryID: number;
  Name: string;
  Type: AutomationModuleType[];
  Module: GeneralModule[];
  Description: string;
  Subject: string;
  
}

export class PostCreateNewTemplateRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
        TemplateID: new UntypedFormControl(''),            
        TemplateCategoryID: new UntypedFormControl(''),
        Name: new UntypedFormControl('',Validators.required),
        Type: new UntypedFormControl(Validators.required),
        Module: new UntypedFormControl({ value:'', disabled:true }),
        Description: new UntypedFormControl(''),
        Subject: new UntypedFormControl('',Validators.required),
       
    });
  }

  public value: PostCreateNewTemplateRequest;

  public setValue(value: GetCreateNewTemplateResponse)
  {
    const request = new PostCreateNewTemplateRequest();
    let type = new  AutomationModuleType();
    if(value.Template!=null)
    {
        value.Template.forEach(element => {
        request.TemplateID = element.TemplateID;        
        request.TemplateCategoryID = element.TemplateCategoryID;
        request.Name = element.Name;
        request.Description = element.Description;
        request.Subject = element.Subject; 
        
        if(element.TemplateCategoryID==1)
        {
          if(element.AutomationModuleTypeID!=null && element.AutomationModuleTypeID!=0) 
          {
            type = value.ModuleTypes.find(type=>type.AutomationModuleTypeID == element.AutomationModuleTypeID);
          }
        } 
        
      });
    }
    else
    {
      request.TemplateID = "";     
      request.TemplateCategoryID =-1;
      request.Name = "";      
      request.Description = "";
      request.Subject = "";
    }
  
    
    request.Module = value.Modules;
    request.Type = value.ModuleTypes;
    super.setValue(request);

    if(type.AutomationModuleTypeID)
    {
      this.get("Type").setValue(type);
    }


  }
}

export class PostCreateNewTemplateResponse
{
  // TemplateID: string;
  // PortalID: number;
  // ModulePortalID: number;
  Template: GetCreateNewTemplateResponse;
}
