import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetSentNotificationContentRequest, GetSentNotificationContentResponse,
  GetSentNotificationRequest, GetSentNotificationResponse, ISentNotificationService } from '../@intermediate/SentNotification';

@Injectable()
export class SentNotificationService implements ISentNotificationService
{

  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/SentNotification';
  }

  Post(request: GetSentNotificationRequest): Observable<GetSentNotificationResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<GetSentNotificationResponse>(apiUrl, request);
  }

  Get(request: GetSentNotificationContentRequest): Observable<GetSentNotificationContentResponse>
  {
    const apiUrl = this.serviceUrl + `/Content`;
    return this.http.get<GetSentNotificationContentResponse>(apiUrl, { params: request as any });
  }
}
