import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { MultiChipModule } from '@tei/material';
import { EmailTemplateService } from '../../@services/email-template.service';
import { EmailTemplateComponent } from './email-template.component';
import { TranslateModule } from '@ngx-translate/core';




const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule, SafeHtmlPipeModule];
const MATERIAL_MODULES = [MatSlideToggleModule, MatMenuModule, MatSelectModule, MatDividerModule, MatListModule, MatCheckboxModule, MatCardModule, MatButtonModule, MatInputModule, MatIconModule, MatExpansionModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const TEI_MATERIAL_MODULES = [MultiChipModule];

const COMPONENTS = [EmailTemplateComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IEmailTemplateService', useClass: EmailTemplateService }],
})
export class EmailTemplateModule { }
