import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule, SnackBarModule } from '@tei/material';
import { CreateNewEmailTemplateService } from '../../@services/create-new-email-template.service';
import { EmailTemplateModule } from '../../components/email-template/email-template.module';
import { CreateNewEmailTemplateComponent } from './create-new-email-template.component';
import { TranslateModule } from '@ngx-translate/core';



const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [];
const TEI_MATERIAL_MODULES = [EmailTemplateModule, SnackBarModule, LoadingModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];

@NgModule({
  declarations: [CreateNewEmailTemplateComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: CreateNewEmailTemplateComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'ICreateNewEmailTemplateService', useClass: CreateNewEmailTemplateService }],
})
export class CreateNewEmailTemplateModule { }
