import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from "@angular/forms";


export class FollowUpRequest
{
    FollowUp: FollowUp[] = [];
    DeletedFollowUp: any;
}

export class FollowUpResponse
{
    public FollowUps: Array<FollowUp>;
}
export class FollowUp
{
    AutomationFollowUpID: number;
    Value: number;
    Interval: string;
}

export class DeletedFollowUp
{
    AutomationFollowUpID: number;
}

export class PostFollowUpRequestForm extends UntypedFormGroup
{
    public readonly FollowUp: FollowUpFormArray;
    public readonly DeletedFollowUp: DeletedFollowUpFormArray;

    constructor(value: FollowUp[])
    {
        super({
            FollowUp: new FollowUpFormArray(value),
            DeletedFollowUp: new DeletedFollowUpFormArray(null)
        });

        this.FollowUp = this.get("FollowUp") as FollowUpFormArray;
        this.DeletedFollowUp = this.get("DeletedFollowUp") as FollowUpFormArray;
    }

    public value: FollowUpRequest;
    public setValue(
        value: FollowUp[],
        options?: {
            onlySelf?: boolean;
            emitEvent?: boolean;
        }): void
    {
        let request = new FollowUpRequest();
        request.FollowUp = value;
        request.DeletedFollowUp = null;
        super.setValue(request);
    }
}
export class FollowUpFormArray extends UntypedFormArray
{
    constructor(value: FollowUp[])
    {
        super([]);
    }

    public setValue(value: FollowUp[],
        options?:
            {
                onlySelf?: boolean;
                emitEvent?: boolean;
            }): void 
    {
        if (value)
        {
            value.forEach(element =>
            {
                const FollowUpFrom = new FollowUpForm(element);
                this.push(FollowUpFrom);
            });
        }
    }
}
export class FollowUpForm extends UntypedFormGroup
{
    constructor(value: FollowUp)
    {
        super({
            AutomationFollowUpID: new UntypedFormControl(value?.AutomationFollowUpID),
            Value: new UntypedFormControl(value?.Value, Validators.required),
            Interval: new UntypedFormControl(value?.Interval, Validators.required),
        });

    }

    public value: FollowUp;
    public setValue(value: FollowUp)
    {
        this.setValue(value);
    }
}
export class DeletedFollowUpFormArray extends UntypedFormArray
{
    constructor(value: DeletedFollowUp[])
    {
        super([]);
    }

    public setValue(value: DeletedFollowUp[],
        options?:
            {
                onlySelf?: boolean;
                emitEvent?: boolean;
            }): void 
    {
        if (value)
        {
            value.forEach(element =>
            {
                const FollowUpFrom = new DeletedFollowUpForm(element);
                this.push(FollowUpFrom);
            });
        }
    }
}
export class DeletedFollowUpForm extends UntypedFormGroup
{
    constructor(value: DeletedFollowUp)
    {
        super({
            AutomationFollowUpID: new UntypedFormControl(value?.AutomationFollowUpID),
        });

    }

    public value: FollowUp;
    public setValue(value: FollowUp)
    {
        this.setValue(value);
    }
}
