import { ChangeDetectorRef, Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { ConfirmDialogService, DialogData } from '@tei/material';
import { GetEmailTemplateRequest, GetEmailTemplateResponse, IEmailTemplateService, PostEmailTemplateRequestForm } from '../../@intermediate/email-template';
import { GetTemplateDetailsEmailTemplateRequest, GetTemplateDetailsEmailTemplateResponse } from '../../@intermediate/email-template/models/get-template-details-email-template.model';


@Component({
  selector: 'tei-automated-notification-email-template',
  templateUrl: './email-template.component.html',
  styleUrls: ['./email-template.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(EmailTemplateComponent)
  ]
})
export class EmailTemplateComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetEmailTemplateResponse;
  form: PostEmailTemplateRequestForm;
  template: GetTemplateDetailsEmailTemplateResponse;
  automationID: number;
  automationType: number;
  categoryID: number;
  editTemplate: boolean;
  subjectExpanded: boolean = false;
  groupName: string;
  panelOpenStateSubject: boolean;
  panelOpenState: boolean;
  panelOpenStateTemplate: boolean;
  confirmationEmails: Array<string> = [];

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IEmailTemplateService') private EmailTemplateService: IEmailTemplateService,
    activatedRoute: ActivatedRoute,
    private confirmdialogService: ConfirmDialogService,
    private router: Router,
    private cdr: ChangeDetectorRef,
    private teiTextTranslateService: TranslateService,
  )
  {
    super(_errorStateMatcher);

    activatedRoute.params.subscribe(params =>
    {

      if (params['automation-id'] && params['automation-type-id'])
      {
        this.automationID = params['automation-id'];
        this.automationType = params['automation-type-id'];
        this.categoryID = params['category-id'];
        this.groupName = params['group-name'];
      }
    });
  }

  ngOnInit()
  {
    this.form = new PostEmailTemplateRequestForm(null);
    const request = new GetEmailTemplateRequest();
    request.AutomationID = this.automationID;
    request.AutomationType = this.automationType;
    request.CategoryID = this.categoryID;
    this.load(request);
    this.ngOnChanges();
  }


  ngOnChanges(): void
  {
    this.form.valueChanges.subscribe(value =>
    {
      this.model.FromName = value.FromName;
      this.model.FromEmail = value.FromEmail;
      this.model.ConfirmationAddresses = value.ConfirmationAddresses;
      if (this.template)
      {
        this.template.Subject = value.Subject;
      }
    });


  }

  getconfirmationEmails()
  {
    if (this.model?.ConfirmationAddresses)
      this.confirmationEmails = this.model.ConfirmationAddresses.split(',');
    else
      this.confirmationEmails = [];
  }

  private load(request: GetEmailTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetEmailTemplateResponse();
    }

    this.EmailTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.model.AutomationID = this.automationID;
      this.model.CategoryID = this.categoryID;
      this.editTemplate = response.IsCustomized;
      if (response.TemplateID)
      {
        this.template = new GetTemplateDetailsEmailTemplateResponse();
        this.template.Body = response.Body;
        this.template.Subject = response.Subject;
      }
      this.form.setValue(this.model);
      this.getconfirmationEmails();
    });
  }

  templateSelection()
  {
    let request = new GetTemplateDetailsEmailTemplateRequest();
    let template = this.form.get("Templates").value;
    request.TemplateID = template.TemplateID;
    const customized = this.form.get("IsCustomized").value;
    request.AutomationID = this.automationID;
    request.CategoryID = this.categoryID;
    request.IsCustomized = customized;
    if (request.TemplateID != "")
    {
      this.EmailTemplateService.GetTemplateDetailsEmailTemplate(request).subscribe(response =>
      {
        this.template = response;
        this.form.get("Subject").setValue(this.template.Subject);
      });
    }
  }

  selectedToken(event)
  {
    let currentValue = this.form.controls["Subject"].value == null ? "" : this.form.controls["Subject"].value;
    this.form.controls["Subject"].setValue(currentValue + ' ' + "[" + event + "]");
  }


  customizeTemplate()
  {
    const customized = this.form.get("IsCustomized").value;

    if (customized)
    {
      const dialogData: DialogData = {
        title: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.EMAIL_TEMPLATE.ENABLE_CUSTOMIZE_TEMPLATE'),
        message: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.EMAIL_TEMPLATE.CUSTOMIZED_TEMPLATES_NO_LONGER_RECEIVES'),
        textOkayButton: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.SHARED.CONFIRM'),
        textCancelButton: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.SHARED.CANCEL')
      };

      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.subjectExpanded = true;
          this.editTemplate = true;
          this.templateSelection();
        }
        else
        {
          this.subjectExpanded = false;
          this.form.get("IsCustomized").setValue(false);
          this.templateSelection();
        }
      });
    }
    else
    {
      this.editTemplate = false;
      this.subjectExpanded = false;
      this.templateSelection();
    }
  }

  editEmailTemplate()
  {
    let automationCategoryTemplateID = this.form.get("AutomationCategoryTemplateID").value;
    this.router.navigate(['/office/marketing/notifications/edit-email-template/manage/', this.automationID, automationCategoryTemplateID, this.automationType, this.categoryID, this.groupName]);
  }

  addEmailChip(emails: Array<string>): void
  {
    this.form.get("ConfirmationAddresses").setValue(emails.join(','));
  }
}
