export class GetCampaignsRequest
{
  Filter?: string;
  Status: Array<string>;
  PageNumber: number;
  PageCount: number;
  SortOrder: any;
  SortType: any;
}
export class GetCampaignsResponse
{
  Campaigns: Campaign[];
  TotalCount: number;
}

export class Campaign
{
  Status: CampaignStatus;
  Name: string;
  Date: Date;
  Recipients?: number;
  Views?: number;
  Clicked?: number;
}

export enum CampaignStatus
{
  Drafts, Sent, Scheduled, SentCancelled
}

export enum SortType
{
  Ascending = 0,
  Descending = 1
}
export class CampaignsMetadata
{
  readonly Columns: string[] = ['Details', 'Status', 'MobileMode'];
}
