import { Location } from '@angular/common';
import { Component, ElementRef, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { AutomationModuleType, GeneralModule, GetCreateNewTemplateRequest, GetCreateNewTemplateResponse, ICreateNewTemplateService, PostCreateNewTemplateRequest, PostCreateNewTemplateRequestForm } from '../../@intermediate/create-new-template';
import { Template } from '../../@intermediate/templates';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'tei-automated-notification-pages-create-new-template',
  templateUrl: './create-new-template.component.html',
  styleUrls: ['./create-new-template.component.scss']
})

export class CreateNewTemplateComponent implements OnInit
{
  model: GetCreateNewTemplateResponse;
  form: PostCreateNewTemplateRequestForm;
  templateName: string;
  automationType: number;
  templateID: string;
  moduleSelected: number;
  tabID: number;
  create: boolean;
  hideType: boolean = false;
  toSelect: GeneralModule;
  templateCategoryID: number;
  getTempResponse = new GetCreateNewTemplateResponse;
  template: Template[];
  templateTypeMessage: string;
  selectedAutomatedModuleType: number;
  panelOpenStateSubject: boolean;
  panelOpenState: boolean;
  panelOpenStateTemplate: boolean;
  isLoading: boolean = false;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.NOTIFICATIONS.NOTIFICATIONS'),
      relativeUrl: '../../../../../'
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.TEMPLATES.EMAIL_TEMPLATES'),
      relativeUrl: '../../../../../templates'
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.TEMPLATES.NEW'),
      relativeUrl: ''
    }
  ];

  constructor(
    @Inject('ICreateNewTemplateService') private CreateNewTemplateService: ICreateNewTemplateService,
    activatedRoute: ActivatedRoute,
    private router: Router,
    private sanitizer: DomSanitizer,
    private snackbarService: SnackBarService,
    private translate: TranslateService,
    private location: Location
  )
  {
    this.form = new PostCreateNewTemplateRequestForm();

    activatedRoute.params.subscribe(params =>
    {
      if (params['automation-type-id'] && params['automation-name'] && params['tab-id'])
      {
        this.automationType = params['automation-type-id'];
        this.templateName = params['automation-name'];
        this.tabID = params['tab-id'];
        this.create = true;
      }

      if (params['template-id'] && params['template-category-id'] && params['automation-type-id'])
      {
        this.templateID = params['template-id'];
        this.automationType = params['automation-type-id'];
        this.templateCategoryID = params['template-category-id'];
        this.create = false;
      }
    });
  }

  ngOnInit()
  {

    const request = new GetCreateNewTemplateRequest();
    this.moduleSelected = this.automationType;
    request.ModuleID = this.automationType;
    request.TemplateID = this.templateID == undefined ? "" : this.templateID;
    if (this.tabID == 0 || this.templateCategoryID == 0)
    {
      this.hideType = true;
    }
    this.load(request);
    this.ngOnChanges();
  }

  ngOnChanges()
  {

    this.form.valueChanges.subscribe(value =>
    {
      if (this.model.Template)
      {
        this.model.Template.forEach(element =>
        {
          element.Name = value.Name;
          element.Description = value.Description;
          element.Subject = value.Subject;
        });
      }
      else
      {
        let tem = new Template();
        tem.Name = value.Name;
        tem.Description = value.Description;
        tem.Subject = (value.Subject) ? value.Subject : "";
        tem.Body = (value.Body) ? value.Body : "";
        this.template = [tem];
        this.template.push(tem);
        this.model.Template = this.template;
      }

    });
  }

  getSanitizedHtml(html: string)
  {
    return this.sanitizer.bypassSecurityTrustHtml(html);
  }

  private load(request: GetCreateNewTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetCreateNewTemplateResponse();
    }

    const tab = this.tabID;
    let tempTemplate = null;
    this.CreateNewTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;

      if (this.model && this.model.Template && this.model.Template.length > 0)
      {
        this.breadCrumbData[this.breadCrumbData.length - 1].title = this.model?.Template[0]?.Name;
      }

      tempTemplate = response.Template;
      this.form.setValue(response);

      if (tempTemplate || this.tabID == 0)
      {
        if (response.Template[0].AutomationModuleTypeID)
        {
          this.selectedAutomatedModuleType = response.Template[0].AutomationModuleTypeID;
          this.getTypeMessage(response.Template[0].AutomationModuleTypeID);
        }
      }
      //const toSelect = this.model.Modules.find(c => c.ModulePortalID == this.moduleSelected);
      const toSelect = this.model.Modules.find(c => c.ModuleID == this.moduleSelected);
      this.form.get('Module').setValue(toSelect);
      if (this.templateName)
      {
        this.form.get("Name").setValue(this.templateName);
        this.form.get("TemplateCategoryID").setValue(this.tabID);
      }
      if (this.tabID == 0 || this.templateCategoryID == 0)
      {
        this.toSelect = new GeneralModule();
        this.toSelect.ModulePortalID = -1;
        this.toSelect.Name = "Campaign";
        this.model.Modules.push(this.toSelect);
        let automationModuletype = new AutomationModuleType();
        automationModuletype.AutomationModuleTypeID = -1;
        automationModuletype.Type = "Campaign";
        this.form.get('Module').setValue(this.toSelect);
        this.form.get('Type').setValue(automationModuletype);
      }

      if (this.create)
      {
        this.save();
      }

    });

  }

  save(showSuccessMessage: boolean = true)
  {
    if (this.create || !this.selectedAutomatedModuleType)
    {
      this.form.get("Type").patchValue({ AutomationModuleTypeID: null, Type: "" });
    }
    let formValue = this.form.getRawValue() as PostCreateNewTemplateRequest;
    this.isLoading = true;
    this.CreateNewTemplateService.Post(formValue).subscribe({
      next: (response) =>
      {
        this.isLoading = false;
        this.templateID = response.Template.Template[0].TemplateID;
        this.model = response.Template;
        this.form.setValue(response.Template);
        //const toSelect = this.model.Modules.find(c => c.ModulePortalID == this.moduleSelected);
        const toSelect = this.model.Modules.find(c => c.ModuleID == this.moduleSelected);
        this.form.get('Module').setValue(toSelect);
        if (this.tabID == 0 || this.templateCategoryID == 0)
        {
          this.toSelect = new GeneralModule();
          this.toSelect.ModulePortalID = -1;
          this.toSelect.Name = "Campaign";
          this.model.Modules.push(this.toSelect);
          let automationModuletype = new AutomationModuleType();
          automationModuletype.AutomationModuleTypeID = -1;
          automationModuletype.Type = "Campaign";
          this.form.get('Module').setValue(this.toSelect);
          this.form.get('Type').setValue(automationModuletype);
        }

        if (response.Template.Template[0].TemplateID && (this.create))
        {
          this.create = false;
          if (showSuccessMessage)
            this.snackbarService.showSuccess('Template created.', '', null, 'left');
          this.router.navigate(['/office/marketing/notifications/create-new-template/manage/', response.Template.Template[0].TemplateID, this.tabID, this.automationType]);
        }
        else
        {
          if (showSuccessMessage)
            this.snackbarService.showSuccess('Saved successfully.', '', null, 'left');
        }
      },
      error: (error) => this.isLoading = false
    });
  }


  selectedToken(event)
  {
    let currentValue = this.form.controls["Subject"].value == null ? "" : this.form.controls["Subject"].value;
    this.form.controls["Subject"].setValue(currentValue + ' ' + "[" + event + "]");
  }

  editTemplate()
  {
    if (this.templateID)
    {
      this.save(false);
      this.router.navigate(['/office/marketing/notifications/template-builder/manage/', this.templateID, this.templateCategoryID, this.automationType]);
    }
  }

  typeChange(event)
  {
    this.selectedAutomatedModuleType = event.value.AutomationModuleTypeID;
    this.getTypeMessage(event.value.AutomationModuleTypeID);
  }

  getTypeMessage(automationModuleTypeID)
  {
    if (automationModuleTypeID == 0 || automationModuleTypeID == -1 || automationModuleTypeID == null)
    {
      this.templateTypeMessage = automationModuleTypeID ? "" : (this.model.TemplateModuleTypeDescription?.[0]?.TemplateModuleDescription ?? "");
    }
    else
    {
      const typeMessage = this.model.TemplateModuleTypeDescription?.filter(x => x.AutomationModuleTypeID == automationModuleTypeID);
      this.templateTypeMessage = typeMessage && typeMessage[0]
        ? typeMessage[0].TemplateModuleDescription
        : "";
    }
  }

  goBack(): void
  {
    this.location.back();
  }

}
