import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetReportsRequest, GetReportsResponse, IReportsService } from '../@intermediate/reports';

@Injectable({
  providedIn: 'root'
})

export class ReportsService implements IReportsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Reports';
  }

  public Get(request: GetReportsRequest): Observable<GetReportsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetReportsResponse>(apiUrl, { params: request as any });
  }
}
