import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { RouterModule } from '@angular/router';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { CreateNewAutomationTypeService } from '../../@services/create-new-automation-type.service';
import { CreateNewAutomationTypeComponent } from './create-new-automation-type.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MATERIAL_MODULES = [MatCardModule, MatButtonModule, MatDialogModule, MatInputModule, MatIconModule, MatRadioModule, MatDividerModule];
const TEI_MATERIAL_MODULES = [];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];

const COMPONENTS = [CreateNewAutomationTypeComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'ICreateNewAutomationTypeService', useClass: CreateNewAutomationTypeService }],
})
export class CreateNewAutomationTypeModule { }
