import { Component, EventEmitter, OnInit, Output, SkipSelf } from "@angular/core";
import { UntypedFormArray } from "@angular/forms";
import { ErrorStateMatcher } from "@angular/material/core";
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from "@tei/core";
import { DeletedReminder, GetReminderResponse, PostReminderRequestForm, Reminder, ReminderInterval } from "../../@intermediate/reminder";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "tei-reminder",
  templateUrl: "./reminder.component.html",
  styleUrls: ["./reminder.component.scss"],
  providers: [
    ngFormGroupControlValueAccessorProviders(ReminderComponent),
  ],
})

export class ReminderComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{

  model: GetReminderResponse;
  form: PostReminderRequestForm;
  intervals: ReminderInterval[];


  constructor(@SkipSelf() protected _errorStateMatcher: ErrorStateMatcher, private teiTextTranslateService: TranslateService)
  {
    super(_errorStateMatcher);
    this.intervals = this.getReminderIntervals();
    this.form = new PostReminderRequestForm(undefined);
  }


  ngOnInit()
  {

  }

  addNotification()
  {
    if (this.getDisable())
    {
      return false;
    }

    let reminderFormArray = this.form.get("Reminder") as UntypedFormArray;
    let reminder = new Reminder();
    reminder.AutomationReminderID = -1;
    reminder.Interval = "H";
    reminder.Value = 24;
    reminderFormArray.setValue([reminder]);
  }

  removeNotification(index: number, control: any)
  {
    let deletedReminder = new DeletedReminder();
    deletedReminder.AutomationReminderID = control.value.AutomationReminderID;
    this.form.DeletedReminder.setValue([deletedReminder]);
    this.form.Reminder.removeAt(index);
  }


  getDisable()
  {
    this.form.Reminder.markAsTouched();
    return ((this.form.Reminder.controls.length >= 3));
  }

  getReminderIntervals(): ReminderInterval[]
  {
    let intervals =
      [
        {
          Value: "H", Text: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.HRS')
        },
        {
          Value: "D", Text: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.DAYS')
        },
        {
          Value: "M", Text: this.teiTextTranslateService.instant('AUTOMATED_NOTIFICATION.CREATE_NEW_AUTOMATION.MINS')
        },
      ];

    return intervals;
  }

}


