import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { ControlValueAccessor, Validator } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ActivatedRoute } from '@angular/router';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetPortalMessageTemplateRequest, GetPortalMessageTemplateResponse, IPortalMessageTemplateService, PostPortalMessageTemplateRequestForm, UserProfile } from '../../@intermediate/portal-message-template';
import { GetUserProfilePortalMessageTemplateRequest } from '../../@intermediate/portal-message-template/models/get-user-profile-portal-message-template.model';



@Component({
  selector: 'tei-automated-notification-portal-message-template',
  templateUrl: './portal-message-template.component.html',
  styleUrls: ['./portal-message-template.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(PortalMessageTemplateComponent)
  ]
})
export class PortalMessageTemplateComponent extends AbstractFormGroupControlValueAccessor implements OnInit, ControlValueAccessor, Validator
{
  model: GetPortalMessageTemplateResponse;
  form: PostPortalMessageTemplateRequestForm;
  automationType: number;
  automationName: string;
  postPortalMessage: GetPortalMessageTemplateResponse;
  FromUserID: number;
  automationID: number;
  categoryID: number;
  panelOpenState: boolean = false;
  deployUrl: string = "http://teiwebdev92.local/";
  userProfile: UserProfile;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IPortalMessageTemplateService') private PortalMessageTemplateService: IPortalMessageTemplateService,
    activatedRoute: ActivatedRoute,
  )
  {
    super(_errorStateMatcher);

    activatedRoute.params.subscribe(params =>
    {
      if (params['automation-type-id'] && params['automation-name'])
      {

        this.automationType = params['automation-type-id'];
        this.categoryID = params['category-id'];
      }

      if (params['automation-id'] && params['automation-type-id'])
      {
        this.automationID = params['automation-id'];
        this.automationType = params['automation-type-id'];
        this.categoryID = params['category-id'];
      }

    });

    // this.deployUrl = window.location.origin + '/';
  }

  ngOnInit()
  {
    this.postPortalMessage = new GetPortalMessageTemplateResponse();
    this.form = new PostPortalMessageTemplateRequestForm(this.postPortalMessage);
    const request = new GetPortalMessageTemplateRequest();
    request.AutomationType = this.automationType;
    request.CategoryID = this.categoryID == undefined ? -1 : this.categoryID;
    request.AutomationID = this.automationID == undefined ? -1 : this.automationID;
    //this.FromUserID= (this.userId==undefined) ? -1 : this.userId;
    this.load(request);
    this.onChanges();

  }

  private load(request: GetPortalMessageTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetPortalMessageTemplateResponse();
    }

    this.userProfile = new UserProfile();

    this.PortalMessageTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.FromUserID = response.User.UserID;

      this.form.setValue(this.model);
      this.userProfile = response.UserProfile;
    });
  }

  onChanges(): void
  {
    const request = new GetUserProfilePortalMessageTemplateRequest();
    this.form.get("User").valueChanges.subscribe(val =>
    {
      request.UserID = val.UserID;
      this.FromUserID = val.UserID;
      this.PortalMessageTemplateService.GetUserProfile(request).subscribe(response =>
      {
        this.model.UserProfile = response.UserProfile;
      });
    });
  }

  selectedToken(event)
  {
    let currentValue = this.form.controls["MessageContent"].value == null ? "" : this.form.controls["MessageContent"].value;
    this.form.controls["MessageContent"].setValue(currentValue + ' ' + "[[" + event + "]]");
  }

}
