import { Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
    selector: '[appNoSpecialChars]'
})
export class NoSpecialCharsDirective
{
    private regex: RegExp = new RegExp(/^[1-365]*$/); // Allow only numbers


    constructor(private el: ElementRef) { }

    @HostListener('input', ['$event'])
    onInputChange(event: Event)
    {
        const input = this.el.nativeElement as HTMLInputElement;
        let value = input.value;

        // If the input value contains non-digit characters, remove them
        if (!this.regex.test(value))
        {
            value = value.replace(/[^0-9]/g, '');
        }

        // Ensure the value is between 1 and 1000
        if (value !== '' && (Number(value) < 1 || Number(value) > 1000))
        {
            value = value.slice(0, -1);
        }

        input.value = value;
    }

    @HostListener('keypress', ['$event'])
    onKeyPress(event: KeyboardEvent)
    {
        const input = event.target as HTMLInputElement;
        if (!this.regex.test(event.key) && Number(input.value + event.key) <= 0)
        {
            event.preventDefault();
        }
    }

    @HostListener('paste', ['$event'])
    onPaste(event: ClipboardEvent)
    {
        const input = event.target as HTMLInputElement;
        const clipboardData = event.clipboardData?.getData('text');
        if (clipboardData && !clipboardData.match(this.regex) && Number(input.value) <= 0)
        {
            event.preventDefault();
        }
    }
}
