import { Component, forwardRef, Inject, OnInit } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { SnackBarService } from '@tei/material';
import { GetAutomateResponse } from '../../@intermediate/automate';
import { GetAutomationMatDialogRequestForm } from '../../@intermediate/automation-mat-dialog';
import { Template } from '../../@intermediate/templates';


@Component({
  selector: 'tei-automated-notification-pages-template-mat-dialog',
  templateUrl: './template-mat-dialog.component.html',
  styleUrls: ['./template-mat-dialog.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TemplateMatDialogComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => TemplateMatDialogComponent),
      multi: true
    }
  ]
})
export class TemplateMatDialogComponent implements OnInit
{
  form: GetAutomationMatDialogRequestForm;
  automateTypes: GetAutomateResponse;
  automateLabelName: string;
  tabSelectedIndex: number = 0;
  automationForm: boolean = false;
  campaignForm: boolean;
  templates: Template[];

  constructor(
    public dialogRef: MatDialogRef<TemplateMatDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    activatedRoute: ActivatedRoute,
    private router: Router,
    private snackbarService: SnackBarService
  )
  {
    this.form = new GetAutomationMatDialogRequestForm();
    this.automateTypes = new GetAutomateResponse();
    this.automateTypes.AutomatedModuleFilters = data.automateTypes.AutomatedModuleFilters;
    this.automateTypes.Automations = data.automateTypes.Automations;
    this.templates = data.automateTypes?.Templates;
  }

  ngOnInit()
  {
    this.automateLabelName = "Template Name";
    this.form.controls["CampaignTemplateName"].setValidators([Validators.required]);
    this.form.controls["NewAutomation"].disable();
    this.form.updateValueAndValidity();
  }
  onBackClick(event): void
  {
    this.dialogRef.close();
    event.preventDefault();
  }

  selectedTab(event)
  {
    this.tabSelectedIndex = event;
    if (this.tabSelectedIndex == 0)
    {
      this.form.controls["CampaignTemplateName"].enable();
      this.form.controls["CampaignTemplateName"].setValidators([Validators.required]);
      this.form.updateValueAndValidity();
      this.form.controls["NewAutomation"].disable();
    }
    else
    {
      this.form.controls["NewAutomation"].enable();
      this.form.controls["CampaignTemplateName"].disable();
      this.form.updateValueAndValidity();
    }
  }

  createNewAutomation(createnewForm: GetAutomationMatDialogRequestForm)
  {
    if (this.tabSelectedIndex == 0)
    {
      let selectedModuleType = 0;
      let campaignTemplate = createnewForm.controls["CampaignTemplateName"].value;
      this.dialogRef.close(createnewForm);
      this.router.navigate(['/office/marketing/notifications/create-new-template/create/', selectedModuleType, campaignTemplate, this.tabSelectedIndex]);
    }
    else
    {
      let automationName = createnewForm.value.NewAutomation["AutomationName"];
      let selectedModuleType = createnewForm.value.NewAutomation["SelectedModuleType"];
      let temaplte = this.templates?.find(c => c.Name == automationName && c.ModuleID == selectedModuleType);

      if (temaplte)
      {
        this.snackbarService.showError("Template Name already exists.", '', null, 'left');
      }
      else
      {
        this.dialogRef.close(createnewForm);
        this.router.navigate(['/office/marketing/notifications/create-new-template/create/', selectedModuleType, automationName, this.tabSelectedIndex]);
      }
    }

  }

  public onTouched: () => void = () => { };

  writeValue(val: any): void
  {
    val && this.form.setValue(val, { emitEvent: false });
  }
  registerOnChange(fn: any): void
  {
    this.form.valueChanges.subscribe(fn);
  }
  registerOnTouched(fn: any): void
  {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void
  {
    isDisabled ? this.form.disable() : this.form.enable();
  }

  validate(c: AbstractControl): ValidationErrors | null
  {
    return this.form.valid ? null : { invalidForm: { valid: false, message: "Form fields are invalid" } };
  }
}
