import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
//import { UsersModule } from '@tei/general-shared';
import { MatBottomSheetModule } from '@angular/material/bottom-sheet';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { PortalMessageTemplateService } from '../../@services/portal-message-template.service';
import { PortalMessageTemplateComponent } from './portal-message-template.component';


const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatListModule, MatMenuModule, MatCardModule, MatButtonModule, MatInputModule, MatIconModule, MatExpansionModule, MatChipsModule, MatBottomSheetModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const TEI_MATERIAL_MODULES = [
  //UsersModule,
];

const COMPONENTS = [PortalMessageTemplateComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],

  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IPortalMessageTemplateService', useClass: PortalMessageTemplateService }],
})
export class PortalMessageTemplateModule { }
