import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';


export class GetAutomateRequest
{
  public AutomatedModuleFilters: AutomatedModuleFilter[];
  public SearchString: string;

}

export class GetAutomateResponse
{
  public AutomatedModuleFilters: AutomatedModuleFilter[];
  public Automations: Automation[];
}




export class GetAutomateRequestForm extends UntypedFormGroup
{

  public readonly AutomatedModuleFilters: GetAutomateRequestFormArray;

  constructor()
  {
    super({

      // AutomatedModuleFilters: new GetAutomateRequestFormArray(),
      AutomatedModuleFilters: new UntypedFormControl([]),
      SearchString: new UntypedFormControl(''),

    });

    //  this.AutomatedModuleFilters = this.get("AutomatedModuleFilters") as GetAutomateRequestFormArray;
  }

  public value: GetAutomateRequest;

  public setValue(value: GetAutomateResponse)
  {
    const request = new GetAutomateRequest();
    request.AutomatedModuleFilters = value.AutomatedModuleFilters;
    request.SearchString = "";

    super.setValue(request);
  }

}





export class GetAutomateRequestFormArray extends UntypedFormArray
{

  ModuleFilter: AutomatedModuleFilter[];

  constructor() 
  {
    super([]);
  }

  public setValue(
    value: AutomatedModuleFilter[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value)
    {


      if (this.controls.length <= 0)
      {
        value.forEach(filtedModule =>
        {

          this.push(new UntypedFormGroup({
            Checked: new UntypedFormControl(true),
            Code: new UntypedFormControl(filtedModule.Code),
            ModuleName: new UntypedFormControl(filtedModule.ModuleName),
            ModuleID: new UntypedFormControl(filtedModule.ModuleID),

          }));



        });
      }
    }

  }

}


export class Automation
{
  AutomationID: number;
  Name: string;
  IsEnabled: boolean;
  ModulePortalID: number;
  ModuleName: string;
  Code: string;
  Selected: boolean;
  ModuleID: number;
}




export class AutomatedModuleFilter
{
  ModuleID: number;
  ModuleName: string;
  Code: string;
  Checked: boolean;
}




export class AutomationMetadata
{
  public readonly Columns: string[] = ['Name', 'Module', 'actions'];
}