import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPortalMessageTemplateRequest, GetPortalMessageTemplateResponse, IPortalMessageTemplateService, PostPortalMessageTemplateRequest, PostPortalMessageTemplateResponse } from '../@intermediate/portal-message-template';
import { GetUserProfilePortalMessageTemplateRequest, GetUserProfilePortalMessageTemplateResponse } from '../@intermediate/portal-message-template/models/get-user-profile-portal-message-template.model';

@Injectable({
  providedIn: 'root'
})

export class PortalMessageTemplateService implements IPortalMessageTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/PortalMessageTemplate';
  }

  public Get(request: GetPortalMessageTemplateRequest): Observable<GetPortalMessageTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetPortalMessageTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPortalMessageTemplateRequest): Observable<PostPortalMessageTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostPortalMessageTemplateResponse>(apiUrl, request);
  }

  public GetUserProfile(request: GetUserProfilePortalMessageTemplateRequest): Observable<GetUserProfilePortalMessageTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '/GetUserProfile';;
    return this.http.get<GetUserProfilePortalMessageTemplateResponse>(apiUrl, { params: request as any });
  }
}
