import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCreateNewTemplateRequest, GetCreateNewTemplateResponse, ICreateNewTemplateService, PostCreateNewTemplateRequest, PostCreateNewTemplateResponse } from '../@intermediate/create-new-template';

@Injectable({
  providedIn: 'root'
})

export class CreateNewTemplateService implements ICreateNewTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/CreateNewTemplate';
  }

  public Get(request: GetCreateNewTemplateRequest): Observable<GetCreateNewTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCreateNewTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCreateNewTemplateRequest): Observable<PostCreateNewTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateNewTemplateResponse>(apiUrl, request);
  }
}
