import { Component, OnInit, Inject } from '@angular/core';
import { IAudienceService, GetAudienceRequest, GetAudienceResponse } from '../../@intermediate/audience';

@Component({
  selector: 'tei-automated-notification-pages-audience',
  templateUrl: './audience.component.html',
  styleUrls: ['./audience.component.scss']
})
export class AudienceComponent implements OnInit
{
  model: GetAudienceResponse;

  constructor(@Inject('IAudienceService') private AudienceService: IAudienceService)
  {
    
  }

  ngOnInit()
  {
    const request = new GetAudienceRequest();
    this.load(request);
  }

  private load(request: GetAudienceRequest)
  {
    if (!this.model)
    {
      this.model = new GetAudienceResponse();
    }

    this.AudienceService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }
}
