import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatSortModule } from '@angular/material/sort';
import { RouterModule } from '@angular/router';
import { ResponsiveTableHeightModule } from '@tei/core';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, CardDialogModule, FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { DefaultTabLayoutModule } from '@tei/theme';
import { SentNotificationHeaderService } from '../../@services/sent-notification-header.service';
import { SentNotificationService } from '../../@services/sent-notification.service';
import { SentNotificationContentDialogComponent } from '../../components/sent-notification/sent-notification-content-dialog/sent-notification-content-dialog.component';
import { SentNotificationMenuComponent } from '../../components/sent-notification/sent-notification-menu/sent-notification-menu.component';
import { SentNotificationSearchHeaderComponent } from '../../components/sent-notification/sent-notification-search-header/sent-notification-search-header.component';
import { SentNotificationComponent } from './sent-notification.component';




const BASE_MODULES = [FormsModule, CommonModule, RouterModule, ReactiveFormsModule, SafeHtmlPipeModule];
const MATERIAL_MODULES = [MatSortModule, MatCardModule, MatTableModule, MatInputModule, MatPaginatorModule, MatMenuModule];
const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, FilterChipsModule, CardDialogModule, LoadingModule, BreadCrumbModule];
const TEI_CORE_MODULES = [ResponsiveTableHeightModule, TeiTextTranslatePipeModule];
const COMPONENTS = [SentNotificationComponent, SentNotificationSearchHeaderComponent, SentNotificationMenuComponent,
  SentNotificationContentDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    DefaultTabLayoutModule,
  ],
  providers: [{ provide: 'ISentNotificationService', useClass: SentNotificationService }, SentNotificationHeaderService],
})
export class SentNotificationModule { }
