import { Component, Inject, OnInit, SkipSelf, ViewChild } from '@angular/core';
import { ControlValueAccessor, Validator } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyListOption as MatListOption } from '@angular/material/legacy-list';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { MatSort } from '@angular/material/sort';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AbstractFormGroupControlValueAccessor, ResponsiveService, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { BreadCrumb, ConfirmDialogService, DialogData } from '@tei/material';
import { Automation, AutomationMetadata, GetAutomateRequest, GetAutomateRequestForm, GetAutomateResponse, IAutomateService } from '../../@intermediate/automate';
import { DeleteAutomateRequest } from '../../@intermediate/automate/models/delete-automate.model';
import { DuplicateAutomateRequest } from '../../@intermediate/automate/models/duplicate-automate.model';
import { TemplateFilterService } from '../../@services/templatefilter.service';
import { TemplatesFiltersDialogComponent } from '../../components/templates-filters-dialog/templates-filters-dialog.component';
import { AutomationMatDialogComponent } from '../automation-mat-dialog/automation-mat-dialog.component';

@Component({
  selector: 'tei-automated-notification-pages-automate',
  templateUrl: './automate.component.html',
  styleUrls: ['./automate.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(AutomateComponent)
  ]
})

export class AutomateComponent extends AbstractFormGroupControlValueAccessor implements OnInit, ControlValueAccessor, Validator
{
  calculateExtraOffset(): number
  {
    if (this.isSmallMobileView)
    {
      return 325;
    } else if (this.isMobileView)
    {
      return 282;
    } else
    {
      return 218;
    }
  }

  model: GetAutomateResponse;
  form: GetAutomateRequestForm;
  AutomationType: number;
  AutomationName: string;
  automationID: number;
  typeChecked: boolean;
  isSmallMobileView: boolean;
  isMobileView: boolean = false;
  isTabletView: boolean = false;
  metadata = new AutomationMetadata();
  datasource: MatTableDataSource<Automation>;
  automationHeader: string = "Select All";
  filterName: string = '';
  filterType: string[] = [];
  filterSearchValue: string = '';
  @ViewChild('allSelected') private allSelected: MatListOption;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.NOTIFICATIONS.NOTIFICATIONS'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.AUTOMATIONS'),
      relativeUrl: ''
    }
  ];

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IAutomateService') private AutomateService: IAutomateService,
    public dialog: MatDialog, private router: Router, private confirmdialogService: ConfirmDialogService,
    private activedRoute: ActivatedRoute,
    private translate: TranslateService,
    private templateFilterService: TemplateFilterService,
    private responsiveService: ResponsiveService)
  {
    super(_errorStateMatcher);
    this.form = new GetAutomateRequestForm();
    const filteredValue = this.templateFilterService.currentFilter.subscribe(filterValue =>
    {
      this.filterFromDialog(filterValue);
    });
  }

  ngOnInit()
  {
    this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isSmallMobileView = mediaBreakpoint === 'xs';
      this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm';
      this.isTabletView = mediaBreakpoint === 'md';
    });
    let request = new GetAutomateRequest();
    request.AutomatedModuleFilters = this.form.value.AutomatedModuleFilters;
    request.SearchString = this.form.value.SearchString;
    this.load(request);
  }


  private load(request: GetAutomateRequest)
  {
    if (!this.model)
    {
      this.model = new GetAutomateResponse();
    }

    this.AutomateService.Get(request).subscribe(response =>
    {
      this.model = response;
      setTimeout(() =>
      {
        this.form.setValue(this.model);
      }, 300);
      this.bindAutomations(this.model.Automations);
    });
  }


  filterAutomate(event): void
  {
    if ('name' in event)
    {
      this.filterName = event.name || '';
    }

    if ('type' in event)
    {
      this.filterType = event.type || [];
    }

    if ('searchValue' in event)
    {
      this.filterSearchValue = event.searchValue || '';
    }

    let filteredData = this.model.Automations;

    if (this.filterSearchValue && this.filterSearchValue.length > 2)
    {
      const searchLower = this.filterSearchValue.toLowerCase();
      filteredData = filteredData.filter(item => String(item.Name).toLowerCase().includes(searchLower) || String(item.ModuleName).toLowerCase().includes(searchLower));
    }

    if (this.filterName)
    {
      const nameLower = this.filterName.toLowerCase();
      filteredData = filteredData.filter(item => String(item.Name).toLowerCase().includes(nameLower));
    }

    if (this.filterType && this.filterType.length > 0)
    {
      filteredData = filteredData.filter(item => this.filterType.includes(item.ModuleName));
    }

    this.datasource = new MatTableDataSource<Automation>(filteredData);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }



  private bindAutomations(automation: Automation[])
  {
    this.datasource = new MatTableDataSource<Automation>(automation.filter(x => x.Selected == true));
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }



  filterTemplates(value)
  {
    let searchString = value.SearchString;
    if (this.model.Automations)
    {
      this.model.Automations.forEach(x => { x.Selected = false; });
      value.AutomationModule.forEach(e =>
      {

        this.model.Automations.filter(f => f.ModuleName == e.ModuleName && e.IsEnabled == true).forEach(f => { f.Selected = true; });
      });
      let templates = this.model.Automations.filter(c => c.Name.toString().toLowerCase().includes(searchString.toLowerCase()));
      this.bindAutomations(templates);
    }
  }

  openDialog(): void
  {

    let dialogRef = this.dialog.open(TemplatesFiltersDialogComponent, {
      panelClass: '',
      height: '100vh',
      width: '100vh',
      maxWidth: '100vh',
      data: { template: false }
    });

    dialogRef.afterClosed().subscribe(value =>
    {
    });
  }

  filterFromDialog(filterValue)
  {
    if (filterValue != null)
    {
      this.filterTemplates(filterValue);
    }
  }

  createAutomation()
  {
    const dialogRef = this.dialog.open(AutomationMatDialogComponent, {
      maxWidth: "700px",
      width: "80%",
      data: {
        automateTypes: this.model
      }
    });
    dialogRef.afterClosed().subscribe(formNewAutomation =>
    {
      const request = new GetAutomateRequest();
      if (formNewAutomation)
      {
        const automationName = formNewAutomation.value.NewAutomation.AutomationName;
        this.AutomationType = formNewAutomation.value.NewAutomation.SelectedModuleType;
        this.AutomationName = formNewAutomation.value.NewAutomation.AutomationName;
        this.router.navigateByUrl('/office/marketing/notifications/automate/create/' + this.AutomationType + '/' + this.AutomationName);
        //this.router.navigate(['create', this.AutomationType, this.AutomationName], { relativeTo: this.activedRoute });
      }
    });
  }


  deleteAutomationTemplate(automationID: number)
  {
    //  index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    //const template = this.model.Automations[index];
    const index = this.model.Automations.findIndex(f => f.AutomationID == automationID);
    const template = this.model.Automations.filter(f => f.AutomationID == automationID)[0];
    const deleteThe = this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DELETE_THE');
    const templateMsg = this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.TEMPLATE');

    const dialogData: DialogData = {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DELETE'),
      message: `${ deleteThe } ${ template.Name } ${ templateMsg }?`,
      textOkayButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DELETE'),
      textCancelButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.CANCEL'),
      buttonColor: 'warn'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DeleteAutomateRequest();
        request.AutomationID = template.AutomationID;

        this.AutomateService.Delete(request).subscribe(response =>
        {
          this.model.Automations.splice(index, 1);
          this.datasource = new MatTableDataSource<Automation>(this.datasource.data.filter(f => f.AutomationID != automationID));
          this.datasource.paginator = this.paginator;
        });
      }
    });

  }

  duplicateAutomationTemplate(automationID: number)
  {

    // index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    // const template = this.model.Automations[index];
    const index = this.model.Automations.findIndex(f => f.AutomationID == automationID);
    const template = this.model.Automations.filter(f => f.AutomationID == automationID)[0];
    const duplicateThe = this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DUPLICATE_THE');
    const duplicateMsg = this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.TEMPLATE');

    const dialogData: DialogData = {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DUPLICATE'),
      message: `${ duplicateThe } ${ template.Name } ${ duplicateMsg }?`,
      textOkayButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DUPLICATE'),
      textCancelButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.CANCEL'),
      buttonColor: 'accent'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DuplicateAutomateRequest();
        request.AutomationID = template.AutomationID;

        this.AutomateService.Duplicate(request).subscribe(response =>
        {
          this.router.navigate(['duplicate', response.AutomationID, response.AutomationType], { relativeTo: this.activedRoute });
        });
      }
    });

  }


  manageAutomation(automationID: number, moduleID: number)
  {
    this.router.navigateByUrl('/office/marketing/notifications/automate/manage/' + automationID + '/' + moduleID);
  }


}


