import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { RouterModule } from '@angular/router';
import { SnackBarModule } from '@tei/material';
import { HtmlBuilderModule } from '@tei/material-html-builder';
import { TemplateBuilderService } from '../../@services/template-builder.service';
import { TemplateBuilderComponent } from './template-builder.component';

const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatInputModule, MatButtonModule];
const TEI_MATERIAL_MODULES = [HtmlBuilderModule, SnackBarModule];

@NgModule({
  declarations: [TemplateBuilderComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: TemplateBuilderComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],

  providers: [
    { provide: 'ITemplateBuilderService', useClass: TemplateBuilderService },
  ],
})
export class TemplateBuilderModule { }
