import { Component, OnInit, Inject, SkipSelf, Output, EventEmitter, ViewChild, Input } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { GetHtmlBuilderRequest, GetHtmlBuilderResponse, PostHtmlBuilderRequest, PostHtmlBuilderRequestForm, PostHtmlBuilderResponse } from '../../@intermediate/html-builder';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { BehaviorSubject, Subject ,ReplaySubject} from 'rxjs';
import { IpEmailBuilderService, Structure, TextBlock, IPEmail, IIPImageUploader, IForRootConf, IPDefaultEmail } from '@tei/ip-email-builder';

@Component({
  selector: 'tei-material-html-builder-html-builder',
  templateUrl: './html-builder.component.html',
  styleUrls: ['./html-builder.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(HtmlBuilderComponent)
  ]
})

export class HtmlBuilderComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{

  isLoading: ReplaySubject<boolean>;
  model: GetHtmlBuilderResponse;
  form: PostHtmlBuilderRequestForm;
  htmlBuilderRequest: PostHtmlBuilderRequest;
  @Output() templateSaved = new EventEmitter<PostHtmlBuilderRequest>();
  @Input() Tokens: string[];
  @Input() UploadImagePath : string;
  @Input() Email: string;
  @Input() ApiKey: string;
  email: BehaviorSubject<IPEmail>;
  
  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    private ngb: IpEmailBuilderService)
  {
    super(_errorStateMatcher);
    this.isLoading = ngb.isLoading;
  }

  ngOnInit()
  {
    this.form = new PostHtmlBuilderRequestForm();
    this.ngb.config.xApiKey = this.ApiKey;
    this.ngb.config.uploadImagePath = window.location.origin + this.UploadImagePath;            
    this.ngb.MergeTags = new Set(this.Tokens);
   
    this.email = new BehaviorSubject(
      new IPEmail({
        structures: JSON.parse(this.Email).structures,
      })
    );
    this.onTemplateSaved();
  }

  onTemplateSaved()
  {
    this.htmlBuilderRequest = new PostHtmlBuilderRequest();
    this.ngb.onSave$.asObservable().subscribe((data) =>
    {
      let structure = data["email"];
      this.htmlBuilderRequest.EmailTemplate = data["template"];      
      this.htmlBuilderRequest.EmailStructure = JSON.stringify(structure);
      this.templateSaved.emit(this.htmlBuilderRequest);
    });
  }

}
