import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { AudienceComponent } from './pages/audience/audience.component';
import { AutomateComponent } from './pages/automate/automate.component';
import { CampaignsComponent } from './pages/campaigns/campaigns.component';
import { CreateNewAutomationComponent } from './pages/create-new-automation/create-new-automation.component';
import { CreateNewEmailTemplateComponent } from './pages/create-new-email-template/create-new-email-template.component';
import { CreateNewPortalMessageComponent } from './pages/create-new-portal-message/create-new-portal-message.component';
import { CreateNewTemplateComponent } from './pages/create-new-template/create-new-template.component';
import { EditEmailTemplateComponent } from './pages/edit-email-template/edit-email-template.component';
import { NotificationsComponent } from './pages/notifications/notifications.component';
import { PagesComponent } from './pages/pages.component';
import { ReportsComponent } from './pages/reports/reports.component';
import { SentNotificationComponent } from './pages/sent-notification/sent-notification.component';
import { TemplateBuilderComponent } from './pages/template-builder/template-builder.component';
import { TemplatesComponent } from './pages/templates/templates.component';

const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: '',
        component: NotificationsComponent,
        data: { RouteData: new RouteData({ Title: 'NOTIFICATIONS' }) }
      },
      {
        path: 'campaigns',
        component: CampaignsComponent,
        data: { RouteData: new RouteData({ Title: 'CAMPAIGNS' }) },
      },
      {
        path: 'automate',
        component: AutomateComponent,
        data: { RouteData: new RouteData({ Title: ' AUTOMATE' }) },
      },
      {
        path: 'sent',
        component: SentNotificationComponent,
        data: { RouteData: new RouteData({ Title: ' SENT' }) },
      },
      {
        path: 'audience',
        component: AudienceComponent,
        data: { RouteData: new RouteData({ Title: ' AUDIENCE' }) },
      },
      {
        path: 'templates',
        component: TemplatesComponent,
        data: { RouteData: new RouteData({ Title: 'TEMPLATES' }) },
      },
      {
        path: 'reports',
        //loadChildren: () => import('../reports/reports.module').then(m => m.ReportsModule),
        component: ReportsComponent,
        data: { RouteData: new RouteData({ Title: 'REPORTS' }) },
      },
      {
        path: 'automate/create/:automation-type-id/:automation-name',
        component: CreateNewAutomationComponent,
        data: { RouteData: new RouteData({ Title: 'Create Automation', CanGoBack: true, BackRoute: '/automate' }) }
      },
      {
        path: 'automate/manage/:automation-id/:automation-type-id',
        component: CreateNewAutomationComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Automation', CanGoBack: true, BackRoute: '/automate' }) }
      },
      {
        path: 'automate/duplicate/:automation-id/:automation-type-id',
        component: CreateNewAutomationComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Automation', CanGoBack: true, BackRoute: '/automate' }) }
      },
      {
        path: 'create-new-portal-message/create/:automation-type-id/:automation-name/:category-id/:group-name',
        component: CreateNewPortalMessageComponent,
        data: { RouteData: new RouteData({ Title: 'Create Portal Message', CanGoBack: true, BackRoute: '/automate' }) }
      },
      {
        path: 'create-new-portal-message/manage/:automation-id/:automation-type-id/:category-id/:group-name',
        component: CreateNewPortalMessageComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Portal Message', CanGoBack: true, BackRoute: '/automate/manage/:automation-id/:automation-type-id' }) }
      },
      {
        path: 'create-new-email-template/manage/:automation-id/:automation-type-id/:category-id/:group-name',
        component: CreateNewEmailTemplateComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Email Template', CanGoBack: true, BackRoute: '/automate/manage/:automation-id/:automation-type-id' }) }
      },
      {
        path: 'create-new-template/create/:automation-type-id/:automation-name/:tab-id',
        component: CreateNewTemplateComponent,
        data: { RouteData: new RouteData({ Title: 'Create Template', CanGoBack: true, BackRoute: '/templates' }) }
      },
      {
        path: 'create-new-template/manage/:template-id/:template-category-id/:automation-type-id',
        component: CreateNewTemplateComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Template', CanGoBack: true, BackRoute: '/templates' }) }
      },
      {
        path: 'create-new-template/duplicate/:template-id/:template-category-id/:automation-type-id',
        component: CreateNewTemplateComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Template', CanGoBack: true, BackRoute: '/templates' }) }
      },
      {
        path: 'template-builder/manage/:template-id/:template-category-id/:automation-type-id',
        component: TemplateBuilderComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Template', CanGoBack: true, BackRoute: '/create-new-template/manage/:template-id/:template-category-id/:automation-type-id' }) }
      },
      {
        path: 'edit-email-template/manage/:automation-id/:automation-category-template-id/:automation-type-id/:category-id/:group-name',
        component: EditEmailTemplateComponent,
        data: { RouteData: new RouteData({ Title: 'Manage Template', CanGoBack: true, BackRoute: '/create-new-email-template/manage/:automation-id/:automation-type-id/:category-id/:group-name' }) }
      }
    ],
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AutomatedNotificationRoutingModule
{
}
