import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { ResponsiveService } from '@tei/core';
import { ChipItemModel, ChipOptionModel, ChipType } from '@tei/material';
import { GetTemplatesFiltersDialogRequest, GetTemplatesFiltersDialogResponse, ITemplatesFiltersDialogService } from '../../@intermediate/templates-filters-dialog';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-common-automated-notification-automate-search-header',
  templateUrl: './automate-search-header.component.html',
  styleUrls: ['./automate-search-header.component.scss']
})
export class AutomateSearchHeaderComponent implements OnInit
{
  chipItemsWithDisplayName = [{ chipName: 'ModuleName', displayName: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.TYPE') }];
  ChipType = ChipType;
  isMobileView: boolean = false;
  model: GetTemplatesFiltersDialogResponse;
  @Output() filterEmitter: EventEmitter<{ searchValue?: string, name?: string, type?: string | string[], }> = new EventEmitter();
  @Output() createAutomation = new EventEmitter();



  constructor(@Inject('ITemplatesFiltersDialogService') private TemplatesFiltersDialogService: ITemplatesFiltersDialogService,
    private responsiveService: ResponsiveService,
    private translate: TranslateService
  ) { }

  ngOnInit(): void
  {
    this.responsiveService.mediaBreakpoint.subscribe(mediaBreakpoint =>
    {
      this.isMobileView = mediaBreakpoint === 'xs' || mediaBreakpoint === 'sm' || mediaBreakpoint === 'md';
    });
    this.load();
  }

  load(): void
  {
    const request = new GetTemplatesFiltersDialogRequest();
    this.TemplatesFiltersDialogService.Get(request).subscribe({
      next: (response: GetTemplatesFiltersDialogResponse) => this.model = response,
      error: err => { }
    });
  }

  enterFilterValue(event: string): void
  {
    this.filterEmitter.emit({ searchValue: event });
  }

  onNameChangeEvent(chipOptionModel: ChipOptionModel): void
  {
    this.filterEmitter.emit({ name: chipOptionModel.firstValue });
  }

  createNewAutomation(): void
  {
    this.createAutomation.emit();
  }

  chipItemValues(selectedValues: ChipItemModel): void
  {
    this.filterEmitter.emit({ type: selectedValues[0].selectedItem });
  }
}