import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAudienceRequest, GetAudienceResponse, IAudienceService } from '../@intermediate/audience';

@Injectable({
  providedIn: 'root'
})

export class AudienceService implements IAudienceService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Audience';
  }

  public Get(request: GetAudienceRequest): Observable<GetAudienceResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAudienceResponse>(apiUrl, { params: request as any });
  }
}
