import { GetPortalMessageTemplateResponse } from '../../portal-message-template';
import { GetEmailTemplateResponse } from '../../email-template';
import { GetPushNotificationTemplateResponse } from '../../push-notification-template';
import { Reminder } from '../../reminder';
import { FollowUp } from '../../follow-up';

export class GetCreateNewAutomationRequest
{
  AutomationID: number;
  AutomationType: number;
}

export class GetCreateNewAutomationResponse
{
  AutomationID: number;
  AutomationType: number;
  AutomationName: string;
  PortalMessageTemplate: GetPortalMessageTemplateResponse[];
  EmailTemplate: GetEmailTemplateResponse[];
  NotificationGroups: NotificationGroup[];
  NotificationTypes: NotificationType[];
  PushNotificationTemplate: GetPushNotificationTemplateResponse[];
  Reminders: Reminder[];
  FollowUps: FollowUp[];
}

export class NotificationGroup
{
  CategoryTypeID: number;
  Code: string;
  Description: string;
}

export class NotificationType
{
  Type: number;
  CategoryTypeID: number;
  CategoryID: number;
  Description: string;
  Message: string;
  Name: string;
  GroupName: string;
  GroupCode: string;
  IsEnabled: boolean;
  Code: string;

}