import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { SnackBarModule } from '@tei/material';
import { HtmlBuilderModule } from '@tei/material-html-builder';
import { EditEmailTemplateService } from '../../@services/edit-email-template.service';
import { EditEmailTemplateComponent } from './edit-email-template.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [];
const TEI_MATERIAL_MODULES = [HtmlBuilderModule, SnackBarModule];

@NgModule({
  declarations: [EditEmailTemplateComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: EditEmailTemplateComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    HtmlBuilderModule
  ],
  providers: [{ provide: 'IEditEmailTemplateService', useClass: EditEmailTemplateService }],
})
export class EditEmailTemplateModule { }
