import { Component, forwardRef, Inject, OnInit } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, NG_VALUE_ACCESSOR, ValidationErrors } from '@angular/forms';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { Router } from '@angular/router';
import { SnackBarService } from '@tei/material';
import { GetAutomateResponse } from '../../@intermediate/automate';
import { GetAutomationMatDialogRequestForm, GetAutomationMatDialogResponse } from '../../@intermediate/automation-mat-dialog';


@Component({
  selector: 'tei-automatednotification-pages-automation-mat-dialog',
  templateUrl: './automation-mat-dialog.component.html',
  styleUrls: ['./automation-mat-dialog.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AutomationMatDialogComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => AutomationMatDialogComponent),
      multi: true
    }
  ]
})
export class AutomationMatDialogComponent implements OnInit
{
  model: GetAutomationMatDialogResponse;
  formCreateNew: GetAutomationMatDialogRequestForm;
  automateTypes: GetAutomateResponse;
  automateLabelName: string;
  constructor(public dialogRef: MatDialogRef<AutomationMatDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private router: Router,
    private snackbarService: SnackBarService,)
  {
    this.formCreateNew = new GetAutomationMatDialogRequestForm();
    this.automateTypes = new GetAutomateResponse();
    this.automateTypes.AutomatedModuleFilters = data.automateTypes.AutomatedModuleFilters;
    this.automateTypes.Automations = data.automateTypes.Automations;
  }

  ngOnInit()
  {
    this.automateLabelName = "Automation Name";
  }

  onBackClick(event): void
  {
    this.dialogRef.close();
    event.preventDefault();
  }

  createNewAutomation(createnewForm: GetAutomationMatDialogRequestForm)
  {
    var automationName = createnewForm.value.NewAutomation["AutomationName"];
    var automationType = createnewForm.value.NewAutomation["SelectedModuleType"];
    let automation = this.automateTypes.Automations.find(c => c.Name == automationName && c.ModuleID == automationType);
    if (automation)
    {
      this.snackbarService.showError("Template Name already exists.", '', null, 'left');
    }
    else
    {
      this.dialogRef.close(createnewForm);
    }

  }

  public onTouched: () => void = () => { };

  writeValue(val: any): void
  {
    val && this.formCreateNew.setValue(val, { emitEvent: false });
  }
  registerOnChange(fn: any): void
  {
    this.formCreateNew.valueChanges.subscribe(fn);
  }
  registerOnTouched(fn: any): void
  {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void
  {
    isDisabled ? this.formCreateNew.disable() : this.formCreateNew.enable();
  }

  validate(c: AbstractControl): ValidationErrors | null
  {
    return this.formCreateNew.valid ? null : { invalidForm: { valid: false, message: "Form fields are invalid" } };
  }
}
