import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCreateNewPortalMessageRequest, GetCreateNewPortalMessageResponse, ICreateNewPortalMessageService, PostCreateNewPortalMessageRequest, PostCreateNewPortalMessageResponse } from '../@intermediate/create-new-portal-message';

@Injectable({
  providedIn: 'root'
})

export class CreateNewPortalMessageService implements ICreateNewPortalMessageService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/CreateNewPortalMessage';
  }

  public Get(request: GetCreateNewPortalMessageRequest): Observable<GetCreateNewPortalMessageResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCreateNewPortalMessageResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCreateNewPortalMessageRequest): Observable<PostCreateNewPortalMessageResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateNewPortalMessageResponse>(apiUrl, request);
  }
}
