import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { GetEditEmailTemplateRequest, GetEditEmailTemplateResponse, IEditEmailTemplateService, PostEditEmailTemplateRequest, PostEditEmailTemplateRequestForm } from '../../@intermediate/edit-email-template';

@Component({
  selector: 'tei-automated-notification-pages-edit-email-template',
  templateUrl: './edit-email-template.component.html',
  styleUrls: ['./edit-email-template.component.scss']
})
export class EditEmailTemplateComponent implements OnInit
{
  model: GetEditEmailTemplateResponse;
  form: PostEditEmailTemplateRequestForm;
  automationTypeID: number;
  automationCategoryTemplateID: string;
  templateRequest: PostEditEmailTemplateRequest;
  automationID: number;
  tokens: string[];
  email: string;
  req: PostEditEmailTemplateRequest;
  htmlBuilderApiKey: string;
  uploadImagepath: string;

  constructor(
    @Inject('IEditEmailTemplateService') private EditEmailTemplateService: IEditEmailTemplateService,
    activatedRoute: ActivatedRoute,
    private router: Router,
    private translate: TranslateService,
    private snackbarService: SnackBarService,
  )
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['automation-id'] && params['automation-category-template-id'])
      {
        this.automationTypeID = params['automation-type-id'];
        this.automationCategoryTemplateID = params['automation-category-template-id'];
        this.automationID = params['automation-id'];
      }
    });
  }

  ngOnInit()
  {
    this.form = new PostEditEmailTemplateRequestForm();
    const request = new GetEditEmailTemplateRequest();
    request.AutomationCategoryTemplateID = this.automationCategoryTemplateID;
    this.load(request);
  }

  private load(request: GetEditEmailTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetEditEmailTemplateResponse();
    }

    this.EditEmailTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.email = response.EmailBodyStructure;
      this.tokens = response.Tokens;
      this.htmlBuilderApiKey = response.ApiKey;
      this.uploadImagepath = response.UploadImagePath;
    });
  }

  save()
  {
    this.EditEmailTemplateService.Post(this.templateRequest).subscribe(response =>
    {
    });
  }

  templateSaved($event)
  {
    this.req = new PostEditEmailTemplateRequest();
    this.req.Body = $event.EmailTemplate;
    this.req.BodyStructure = $event.EmailStructure;
    this.req.AutomationCategoryTemplateID = this.automationCategoryTemplateID;
    this.EditEmailTemplateService.Post(this.req).subscribe(response =>
    {
      this.snackbarService.showSuccess(this.translate.instant('AUTOMATED_NOTIFICATION.EMAIL_TEMPLATE.TEMPLATE_SAVED'), '', null, 'left');
    });

  }
}
