import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ChipOptionModel, ChipType } from '@tei/material';
import { concat, Observable, of, Subject, Subscription } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { GetSentNotificationRequest } from '../../../@intermediate/SentNotification';
import { SentNotificationHeaderService } from '../../../@services/sent-notification-header.service';
import { TranslateService } from '@ngx-translate/core';
import { GetAutomateRequest } from '../../../@intermediate/automate';

@Component({
  selector: 'tei-automated-notification-sent-search-header',
  templateUrl: './sent-notification-search-header.component.html',
  styleUrls: ['./sent-notification-search-header.component.scss']
})
export class SentNotificationSearchHeaderComponent implements OnInit, OnDestroy {
  ChipType = ChipType;
  @Output() enterFilterValueEvent = new EventEmitter<GetSentNotificationRequest>();
  selectedType = '';

  form: UntypedFormGroup;
  dateRange: ChipOptionModel;
  toEmail = '';
  fromEmail = '';

  filterChipArray: Observable<any[]> = of([]);
  activeChipDisplayName = '';
  chipItemsWithDisplayName = [{ chipName: 'Type', displayName: this.translate.instant('AUTOMATED_NOTIFICATION.SENT_NOTIFICATION.TYPE') }];
  selectedChipValues = new Map<string, string>();

  transactionalTypeData;

  subscription = new Subscription();

  constructor(public service: SentNotificationHeaderService, private translate: TranslateService) {
  }

  ngOnInit(): void {
    this.form = new UntypedFormGroup({
      filter: new UntypedFormControl()
    });
    this.loadTypeFilterValues();
  }

  chipItemValues(selectedValues: string): void {
    this.selectedType = selectedValues;
    this.load();
  }

  removeItem($event): void {
    this.selectedType = '';
    this.load();
  }

  onRangeItemChangeEvent(chipOptionModel: ChipOptionModel): void {
    this.dateRange = chipOptionModel;
    this.load();
  }

  onToChangeEvent(chipOptionModel: ChipOptionModel): void {
    this.toEmail = chipOptionModel.firstValue;
    this.load();
  }

  onFromChangeEvent(chipOptionModel: ChipOptionModel): void {
    this.fromEmail = chipOptionModel.firstValue;
    this.load();
  }

  enterFilterValue($event: Event): void {
    $event?.stopPropagation();
    $event?.preventDefault();
    setTimeout(() => { this.load(); }, 300);
  }

  loadTypeFilterValues(): void {
    const request = new GetAutomateRequest();

    this.subscription.add(this.service.Get(request).subscribe(automationResult => {
      this.transactionalTypeData = automationResult.AutomatedModuleFilters.map(filterValue => ({ Type: filterValue.ModuleName }));
    }))
  }

  public load(): void {
    const request = new GetSentNotificationRequest();

    if (this.fromEmail) {
      request.From = this.fromEmail;
    } else { delete request.From; }

    if (this.toEmail) {
      request.To = this.toEmail;
    }
    else { delete request.To; }

    if (this.selectedType) {
      request.Type = this.selectedType;
    } else { delete request.Type; }

    if (this.dateRange && this.dateRange.firstValue) {
      request.FromDate = this.dateRange.firstValue.toLocaleDateString();
      // this.setTimeZiro(new Date(this.dateRange.firstValue)).toLocaleDateString();

      if (this.dateRange.secondValue) {
        const seconDate = new Date(this.dateRange.secondValue);
        // seconDate.setDate(seconDate.getDate() + 1);
        request.ToDate = seconDate.toLocaleDateString();
      }
    }
    const filter = this.form.get('filter').value;
    if (filter) {
      request.Filter = filter;
    }

    this.enterFilterValueEvent.emit(request);
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
