import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from "@angular/forms";
import { GetReminderResponse } from "./get-reminder.model";

export class ReminderRequest
{
    Reminder: Reminder[] = [];
    DeletedReminder: any;
}

export class ReminderResponse
{
    public Reminders: Array<Reminder>;
}

export class Reminder
{
    AutomationReminderID: number;
    Value: number;
    Interval: string;
}

export class DeletedReminder
{
    AutomationReminderID: number;
}



export class PostReminderRequestForm extends UntypedFormGroup
{
    public readonly Reminder: ReminderFormArray;
    public readonly DeletedReminder: DeletedReminderFormArray;

    constructor(value: Reminder[])
    {
        super({
            Reminder: new ReminderFormArray(value),
            DeletedReminder: new DeletedReminderFormArray(null)
        });

        this.Reminder = this.get("Reminder") as ReminderFormArray;
        this.DeletedReminder = this.get("DeletedReminder") as ReminderFormArray;
    }

    public value: ReminderRequest;
    public setValue(
        value: Reminder[],
        options?: {
            onlySelf?: boolean;
            emitEvent?: boolean;
        }): void
    {
        let request = new ReminderRequest();
        request.Reminder = value;
        request.DeletedReminder = null;
        super.setValue(request);
    }
}


export class ReminderFormArray extends UntypedFormArray
{
    constructor(value: Reminder[])
    {
        super([]);
    }

    public setValue(value: Reminder[],
        options?:
            {
                onlySelf?: boolean;
                emitEvent?: boolean;
            }): void 
    {
        if (value)
        {
            value.forEach(element =>
            {
                const reminderFrom = new ReminderForm(element);
                this.push(reminderFrom);
            });
        }
    }
}

export class ReminderForm extends UntypedFormGroup
{
    constructor(value: Reminder)
    {
        super({
            AutomationReminderID: new UntypedFormControl(value?.AutomationReminderID),
            Value: new UntypedFormControl(value?.Value, Validators.required),
            Interval: new UntypedFormControl(value?.Interval, Validators.required),
        });

    }

    public value: Reminder;
    public setValue(value: Reminder)
    {
        this.setValue(value);
    }
}



export class DeletedReminderFormArray extends UntypedFormArray
{
    constructor(value: DeletedReminder[])
    {
        super([]);
    }

    public setValue(value: DeletedReminder[],
        options?:
            {
                onlySelf?: boolean;
                emitEvent?: boolean;
            }): void 
    {
        if (value)
        {
            value.forEach(element =>
            {
                const reminderFrom = new DeletedReminderForm(element);
                this.push(reminderFrom);
            });
        }
    }
}

export class DeletedReminderForm extends UntypedFormGroup
{
    constructor(value: DeletedReminder)
    {
        super({
            AutomationReminderID: new UntypedFormControl(value?.AutomationReminderID),
        });

    }

    public value: Reminder;
    public setValue(value: Reminder)
    {
        this.setValue(value);
    }
}
