import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { GetEmailTemplateResponse, PostEmailTemplateRequestFormArray } from '../../email-template';
import { GetPortalMessageTemplateResponse, PostPortalMessageTemplateRequestFormArray } from '../../portal-message-template';
import { GetPushNotificationTemplateResponse } from '../../push-notification-template';
import { DeletedReminder, Reminder, ReminderFormArray } from '../../reminder';
import { GetCreateNewAutomationResponse, NotificationGroup, NotificationType } from './get-create-new-automation.model';
import { DeletedFollowUp, FollowUp } from '../../follow-up';

export class PostCreateNewAutomationRequest
{
  AutomationID?: number;
  AutomationType: number;
  AutomationName: string;
  PortalMessageTemplate: GetPortalMessageTemplateResponse[];
  EmailTemplate: GetEmailTemplateResponse[];
  PushNotificationTemplate: GetPushNotificationTemplateResponse[];
  NotificationTypes: NotificationType[];
  Reminders: Reminder[];
  DeletedReminders: DeletedReminder[];
  FollowUps: FollowUp[];
  DeletedFollowUps: DeletedFollowUp[];
}

export class PostCreateNewAutomationRequestForm extends UntypedFormGroup
{
  public readonly PortalMessageTemplate: PostPortalMessageTemplateRequestFormArray;
  public readonly EmailTemplate: PostEmailTemplateRequestFormArray;
  public readonly PushNotificationTemplate: PostEmailTemplateRequestFormArray;
  public readonly NotificationTypes: PostNotificationTypeFormArray;
  constructor(
    emailTemplate: NotificationType[],
    portalMessgaeTemplate: NotificationType[],
    pushNotificationTemplate: NotificationType[],
    automationResponse: GetCreateNewAutomationResponse,
    reminder: Reminder[],
    followUp: FollowUp[]
  )
  {



    super({
      AutomationID: new UntypedFormControl(automationResponse != null ? automationResponse.AutomationID : null),
      AutomationType: new UntypedFormControl(automationResponse != null ? automationResponse.AutomationType : null),
      AutomationName: new UntypedFormControl(automationResponse != null ? automationResponse.AutomationName : '', Validators.required),
      NotificationTypes: new PostNotificationTypeFormArray(automationResponse != null ? automationResponse.NotificationTypes : null),
      Reminders: new UntypedFormControl(reminder),
      FollowUps: new UntypedFormControl(followUp)
    });


    this.NotificationTypes = this.get("NotificationTypes") as PostNotificationTypeFormArray;
  }

  public value: PostCreateNewAutomationRequest;

  public setValue(value: GetCreateNewAutomationResponse)
  {
    const request = new PostCreateNewAutomationRequest();

    request.AutomationID = value.AutomationID;
    request.AutomationName = value.AutomationName;
    request.AutomationType = value.AutomationType;
    request.NotificationTypes = value.NotificationTypes;
    request.Reminders = value.Reminders;
    request.FollowUps = value.FollowUps;
    super.setValue(request);
  }


}

export class NotificationGroupForm extends UntypedFormGroup
{
  constructor()
  {
    super(
      {
        CategoryTypeID: new UntypedFormControl(''),
        Code: new UntypedFormControl(''),
        Description: new UntypedFormControl(''),
      });

  }

  public value: NotificationGroup;

  public setValue(value: NotificationGroup)
  {
    super.setValue(value);
  }

}

export class NotificationTypeForm extends UntypedFormGroup
{
  constructor(notificationType: NotificationType)
  {
    super(
      {
        Type: new UntypedFormControl(notificationType.Type),
        CategoryTypeID: new UntypedFormControl(notificationType.CategoryTypeID),
        CategoryID: new UntypedFormControl(notificationType.CategoryID),
        Description: new UntypedFormControl(notificationType.Description),
        Message: new UntypedFormControl(notificationType.Message),
        Name: new UntypedFormControl(notificationType.Name),
        GroupName: new UntypedFormControl(notificationType.GroupName),
        IsEnabled: new UntypedFormControl(notificationType.IsEnabled),
        GroupCode: new UntypedFormControl(notificationType.GroupCode),
        Code: new UntypedFormControl(notificationType.Code),
      });

  }

  public value: NotificationType;

  public setValue(value: NotificationType)
  {
    super.setValue(value);
  }
}


export class PostNotificationTypeFormArray extends UntypedFormArray
{

  constructor(types: NotificationType[])
  {
    super([]);

    if (types)
    {
      this.setValue(types);
    }
  }

  public setValue(
    notificationType: NotificationType[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (notificationType)
    {
      notificationType.forEach(notificationType =>
      {
        const notificationTypeFormControl = new NotificationTypeForm(notificationType);
        this.push(notificationTypeFormControl);

      });
    }


  }
}

export class PostCreateNewAutomationResponse
{
  AutomationID: number;
  AutomationType: number;
}
