import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetEmailTemplateRequest, GetEmailTemplateResponse, IEmailTemplateService, PostEmailTemplateRequest, PostEmailTemplateResponse } from '../@intermediate/email-template';
import { GetTemplateDetailsEmailTemplateRequest, GetTemplateDetailsEmailTemplateResponse } from '../@intermediate/email-template/models/get-template-details-email-template.model';

@Injectable({
  providedIn: 'root'
})

export class EmailTemplateService implements IEmailTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/EmailTemplate';
  }

  public Get(request: GetEmailTemplateRequest): Observable<GetEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetEmailTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostEmailTemplateRequest): Observable<PostEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostEmailTemplateResponse>(apiUrl, request);
  }
  public GetTemplateDetailsEmailTemplate(request: GetTemplateDetailsEmailTemplateRequest): Observable<GetTemplateDetailsEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '/GetTemplateDetails';
    return this.http.get<GetTemplateDetailsEmailTemplateResponse>(apiUrl, { params: request as any });
  }
}
