import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCreateNewEmailTemplateRequest, GetCreateNewEmailTemplateResponse, ICreateNewEmailTemplateService, PostCreateNewEmailTemplateRequest, PostCreateNewEmailTemplateResponse } from '../@intermediate/create-new-email-template';


@Injectable({
  providedIn: 'root'
})

export class CreateNewEmailTemplateService implements ICreateNewEmailTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/CreateNewEmailTemplate';
  }

  public Get(request: GetCreateNewEmailTemplateRequest): Observable<GetCreateNewEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCreateNewEmailTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCreateNewEmailTemplateRequest): Observable<PostCreateNewEmailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateNewEmailTemplateResponse>(apiUrl, request);
  }


}
