import { AfterViewInit, ChangeDetectorRef, Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator, LegacyPageEvent as PageEvent } from '@angular/material/legacy-paginator';
import { MatSort, Sort } from '@angular/material/sort';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, CardDialogComponent } from '@tei/material';
import { Subject, merge } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import
{
  GetSentNotificationContentRequest, GetSentNotificationRequest, GetSentNotificationResponse,
  ISentNotificationService, Notification, SentNotificationMetadata, SortType
} from '../../@intermediate/SentNotification';
import { SentNotificationContentDialogComponent } from '../../components/sent-notification/sent-notification-content-dialog/sent-notification-content-dialog.component';

@Component({
  selector: 'tei-automated-notification-sent',
  templateUrl: './sent-notification.component.html',
  styleUrls: ['./sent-notification.component.scss']
})

export class SentNotificationComponent implements OnInit, AfterViewInit, OnDestroy
{
  name: string;
  model: GetSentNotificationResponse;
  message: string;
  datasource = new Array<Notification>();
  metadata = new SentNotificationMetadata();
  getSentNotificationRequest = new GetSentNotificationRequest();

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  // paging & sorting
  totalCount = 100;
  pageIndex = 0;
  pageSize = 10;
  sortOrder: string;
  sortType: SortType;
  isSmallScreen = false;
  isLoadingResults = false;

  private destroy$ = new Subject();

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.NOTIFICATIONS.NOTIFICATIONS'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.SENT_NOTIFICATION.SENT'),
      relativeUrl: ''
    }
  ];

  constructor(
    @Inject('ISentNotificationService') private SentService: ISentNotificationService,
    public dialog: MatDialog,
    private translate: TranslateService,
    private responsiveService: ResponsiveService,
    private changeDetector: ChangeDetectorRef)
  {
  }

  ngOnInit(): void
  {
    this.responsiveService.mediaBreakpoint.pipe(takeUntil(this.destroy$)).subscribe(screenSize =>
    {
      this.isSmallScreen = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    });

    this.sort.sortChange.subscribe(() =>
    {
      this.pageIndex = 0;
      this.paginator.pageIndex = 0;
    });

    merge(this.sort.sortChange, this.paginator.page).subscribe(data =>
    {
      this.pageIndex = this.paginator.pageIndex;
      this.pageSize = this.paginator.pageSize;
      this.load();
    });
  }

  ngAfterViewInit()
  {
    this.load();
    this.changeDetector.detectChanges();
  }

  public loadHandler(request: GetSentNotificationRequest)
  {
    this.getSentNotificationRequest = request;
    this.load();
  }

  public load()
  {
    this.getSentNotificationRequest.PageNumber = this.pageIndex + 1;
    this.getSentNotificationRequest.PageCount = this.pageSize;

    delete this.getSentNotificationRequest.SortOrder;
    delete this.getSentNotificationRequest.SortType;
    if (this.sort.active && this.sort.direction)
    {
      this.getSentNotificationRequest.SortOrder = this.sort.active;
      this.getSentNotificationRequest.SortType = this.sort.direction === 'desc' ? SortType.Descending : SortType.Ascending;
    }

    if (!this.model)
    {
      this.model = new GetSentNotificationResponse();
    }
    this.isLoadingResults = true;
    this.SentService.Post(this.getSentNotificationRequest).toPromise().then(response =>
    {
      this.model = response;
      this.model.Notifications.forEach(p => p.Receiver = p.To.length > 0 ? p.To[0] : '');
      this.datasource = this.model.Notifications;
      setTimeout(() =>
      {
        this.totalCount = this.model.TotalCount;
        this.paginator.pageIndex = this.pageIndex;
        this.paginator.pageSize = this.pageSize;
        this.paginator.length = this.totalCount;
      }, 300);
    }).finally(() => this.isLoadingResults = false);
  }

  onPaginate(pageEvent: PageEvent)
  {
    this.pageIndex = pageEvent.pageIndex;
    this.pageSize = pageEvent.pageSize;
    this.load();
  }

  sortChange(sortState: Sort)
  {
    // this.datasource.data = [];
    if (sortState.direction === '')
    {
      this.sortOrder = '';
      this.sortType = SortType.Ascending;
      this.load();
      return;
    }

    this.sortOrder = sortState.active;
    this.sortType = sortState.direction === 'asc' ? SortType.Ascending : SortType.Descending;
    this.load();
  }

  previewContent(notification: Notification)
  {
    const request = new GetSentNotificationContentRequest();
    request.NotificationID = notification.NotificationID;
    this.SentService.Get(request).subscribe(response =>
    {
      const dialogRef = this.dialog.open(CardDialogComponent, {
        panelClass: 'sent-notification-content-modal',
        data: {
          component: SentNotificationContentDialogComponent,
          inputs: { content: response.Content }
        },
        width: '600px',
      });

      dialogRef.afterClosed().subscribe((result) => { });
    });
  }

  ngOnDestroy(): void
  {
    this.destroy$.next(null);
    this.destroy$.complete();
  }

}
