import { AfterViewInit, ChangeDetectorRef, Component, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator, LegacyPageEvent as PageEvent } from '@angular/material/legacy-paginator';
import { MatSort, Sort } from '@angular/material/sort';
import { ResponsiveService } from '@tei/core';
import { merge, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { Campaign, CampaignsMetadata, CampaignStatus, GetCampaignsRequest, GetCampaignsResponse, ICampaignsService, SortType } from '../../@intermediate/campaigns';

@Component({
  selector: 'tei-automated-notification-pages-campaigns',
  templateUrl: './campaigns.component.html',
  styleUrls: ['./campaigns.component.scss']
})
export class CampaignsComponent implements OnInit, AfterViewInit, OnDestroy
{
  name: string;
  model: GetCampaignsResponse;
  message: string;
  datasource = new Array<Campaign>();
  metadata = new CampaignsMetadata();
  getSentNotificationRequest = new GetCampaignsRequest();

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  // paging & sorting
  totalCount = 100;
  pageIndex = 0;
  pageSize = 10;
  sortOrder: string;
  sortType: SortType;
  isSmallScreen = false;
  isLoadingResults = false;

  private destroy$ = new Subject();

  constructor(
    @Inject('ICampaignsService') private SentService: ICampaignsService,
    public dialog: MatDialog,
    private responsiveService: ResponsiveService,
    private changeDetector: ChangeDetectorRef)
  {
  }

  ngOnInit(): void
  {
    this.responsiveService.mediaBreakpoint.pipe(takeUntil(this.destroy$)).subscribe(screenSize =>
    {
      this.isSmallScreen = screenSize === 'xs';
    });

    this.sort.sortChange.subscribe(() =>
    {
      this.pageIndex = 0;
      this.paginator.pageIndex = 0;
    });

    merge(this.sort.sortChange, this.paginator.page).subscribe(data =>
    {
      this.pageIndex = this.paginator.pageIndex;
      this.pageSize = this.paginator.pageSize;
      this.load();
    });
  }

  ngAfterViewInit()
  {
    this.load();
    this.changeDetector.detectChanges();
  }

  public loadHandler(request: GetCampaignsRequest)
  {
    this.getSentNotificationRequest = request;
    this.load();
  }

  public load()
  {
    this.getSentNotificationRequest.PageNumber = this.pageIndex + 1;
    this.getSentNotificationRequest.PageCount = this.pageSize;

    delete this.getSentNotificationRequest.SortOrder;
    delete this.getSentNotificationRequest.SortType;
    if (this.sort.active && this.sort.direction)
    {
      this.getSentNotificationRequest.SortOrder = this.sort.active;
      this.getSentNotificationRequest.SortType = this.sort.direction === 'desc' ? SortType.Descending : SortType.Ascending;
    }

    if (!this.model)
    {
      this.model = new GetCampaignsResponse();
    }
    this.isLoadingResults = true;
    this.SentService.Get(this.getSentNotificationRequest).toPromise().then(response =>
    {
      this.model = response;
      this.datasource = this.model.Campaigns;
      setTimeout(() =>
      {
        this.totalCount = this.model.TotalCount;
        this.paginator.pageIndex = this.pageIndex;
        this.paginator.pageSize = this.pageSize;
        this.paginator.length = this.totalCount;
      }, 300);
    }).finally(() => this.isLoadingResults = false);
  }

  onPaginate(pageEvent: PageEvent)
  {
    this.pageIndex = pageEvent.pageIndex;
    this.pageSize = pageEvent.pageSize;
    this.load();
  }

  sortChange(sortState: Sort)
  {
    // this.datasource.data = [];
    if (sortState.direction === '')
    {
      this.sortOrder = '';
      this.sortType = SortType.Ascending;
      this.load();
      return;
    }

    this.sortOrder = sortState.active;
    this.sortType = sortState.direction === 'asc' ? SortType.Ascending : SortType.Descending;
    this.load();
  }

  getStatus(status: CampaignStatus): string
  {
    switch (status)
    {
      case CampaignStatus.Drafts: return 'Drafts';
      case CampaignStatus.Sent: return 'Sent';
      case CampaignStatus.Scheduled: return 'Scheduled';
      case CampaignStatus.SentCancelled: return 'SentCancelled';
      default: return '';
    }
  }

  ngOnDestroy(): void
  {
    this.destroy$.next(null);
    this.destroy$.complete();
  }

}

