import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetTemplatesFiltersDialogResponse } from './get-templates-filters-dialog.model';
import { TemplateCategory, AutomationModule } from '../../templates';

export class PostTemplatesFiltersDialogRequest
{
  TemplateCategories: TemplateCategory[];
  SearchString: string;
  AutomationModule: AutomationModule[];
}

export class PostTemplatesFiltersDialogRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      TemplateCategories: new UntypedFormControl(),
      AutomationModule: new UntypedFormControl(),
      SearchString: new UntypedFormControl(''),
    });
  }

  public value: PostTemplatesFiltersDialogRequest;

  public setValue(
    value: GetTemplatesFiltersDialogResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostTemplatesFiltersDialogRequest();
    request.AutomationModule = value.AutomationModules;
    request.TemplateCategories = value.TemplateCategories;
    request.SearchString = "";
    super.setValue(request, options);
  }
}

export class PostTemplatesFiltersDialogResponse
{

}
