import { UntypedFormGroup, UntypedFormControl, Validators, UntypedFormArray } from '@angular/forms';
import { GetAutomateResponse, AutomatedModuleFilter, GetAutomateRequest } from './get-automate.model';




export class PostAutomateRequest
{
  AutomatedModuleFilters: AutomatedModuleFilter[];
  SearchString: string;
}



export class PostAutomateResponse
{
  public Title: string;
}



export class PostAutomateRequestForm extends UntypedFormGroup
{
  public readonly AutomatedModuleFilters: PostAutomateRequestFormArray;

  constructor()
  {
    super({

      AutomatedModuleFilters: new PostAutomateRequestFormArray(),
      SearchString: new UntypedFormControl('')
    });

    this.AutomatedModuleFilters = this.get("AutomatedModuleFilters") as PostAutomateRequestFormArray;
  }

  public value: PostAutomateRequest;

  public setValue(value: GetAutomateResponse)
  {
    const request = new GetAutomateRequest();
    request.AutomatedModuleFilters = value.AutomatedModuleFilters;
    request.SearchString = "";

    super.setValue(request);
  }
}


export class PostAutomateRequestFormArray extends UntypedFormArray
{

  ModuleFilter: AutomatedModuleFilter[];

  constructor() 
  {
    super([]);
  }

  public setValue(
    value: AutomatedModuleFilter[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value)
    {


      if (this.controls.length <= 0)
      {
        value.forEach(filtedModule =>
        {

          this.push(new UntypedFormGroup({
            Checked: new UntypedFormControl(true),
            Code: new UntypedFormControl(filtedModule.Code),
            ModuleName: new UntypedFormControl(filtedModule.ModuleName),
            ModuleID: new UntypedFormControl(filtedModule.ModuleID),
          }));

        });
      }
    }


  }

}