import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetHtmlBuilderResponse } from './get-html-builder.model';
import { FormatWidth } from '@angular/common';

export class PostHtmlBuilderRequest
{
    public EmailTemplate: string;
    public EmailStructure: string;
}

export class PostHtmlBuilderRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      EmailTemplate: new UntypedFormControl(''),
      EmailStructure : new UntypedFormControl(''),
    });
  }

  public value: PostHtmlBuilderRequest;

  public setValue(
    value: GetHtmlBuilderResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostHtmlBuilderRequest();

    request.EmailTemplate = value.Email;
    request.EmailStructure = value.EmailStructure;
    super.setValue(request, options);
  }
}

export class PostHtmlBuilderResponse
{

}
