import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTemplatesFiltersDialogRequest, GetTemplatesFiltersDialogResponse, ITemplatesFiltersDialogService, PostTemplatesFiltersDialogRequest, PostTemplatesFiltersDialogResponse } from '../@intermediate/templates-filters-dialog';

@Injectable({
  providedIn: 'root'
})

export class TemplatesFiltersDialogService implements ITemplatesFiltersDialogService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/TemplatesFiltersDialog';
  }

  public Get(request: GetTemplatesFiltersDialogRequest): Observable<GetTemplatesFiltersDialogResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetTemplatesFiltersDialogResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTemplatesFiltersDialogRequest): Observable<PostTemplatesFiltersDialogResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostTemplatesFiltersDialogResponse>(apiUrl, request);
  }
}
