import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAutomateRequest, GetAutomateResponse, IAutomateService, PostAutomateRequest, PostAutomateResponse } from '../@intermediate/automate';
import { DeleteAutomateRequest, DeleteAutomateResponse } from '../@intermediate/automate/models/delete-automate.model';
import { DuplicateAutomateRequest, DuplicateAutomateResponse } from '../@intermediate/automate/models/duplicate-automate.model';


@Injectable({
  providedIn: 'root'
})

export class AutomateService implements IAutomateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Automate';
  }

  public Get(request: GetAutomateRequest): Observable<GetAutomateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAutomateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostAutomateRequest): Observable<PostAutomateResponse>
  {
    let apiUrl = this.serviceUrl + "";
    return this.http.post<PostAutomateResponse>(apiUrl, request);
  }

  public Delete(request: DeleteAutomateRequest): Observable<DeleteAutomateResponse>
  {
    const apiUrl = this.serviceUrl + "";
    return this.http.delete<DeleteAutomateResponse>(apiUrl, { params: request as any });
  }

  public Duplicate(request: DuplicateAutomateRequest): Observable<DuplicateAutomateResponse>
  {
    const apiUrl = this.serviceUrl + "/Duplicate";
    return this.http.post<DuplicateAutomateResponse>(apiUrl, request);
  }

}
