import { Template } from '../../templates';
import { AutomatedNotificationToken } from '../../portal-message-template';



export class GetCreateNewTemplateRequest
{
  TemplateID: string;
  ModuleID: number;
}

export class GetCreateNewTemplateResponse
{
  Template: Template[];
  Modules: GeneralModule[];
  ModuleTypes: AutomationModuleType[];
  Tokens: AutomatedNotificationToken[];
  TemplateModuleTypeDescription: TemplateModuleTypeDescription[];
}


export class GeneralModule
{
    Name: string;
    ModulePortalID: number;
    ModuleID: number;
}
    
export class AutomationModuleType
{
  Type: string;
  AutomationModuleTypeID: number | null;
}

export class TemplateModuleTypeDescription
{
    TemplateModuleDescription: string;
    AutomationModuleTypeID: number | null;
}