import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { AutomatedNotificationRoutingModule } from './automated-notification-routing.module';
import { AutomatedNotificationComponent } from './automated-notification.component';
import { AudienceModule } from './pages/audience/audience.module';
import { AutomateModule } from './pages/automate/automate.module';
import { CampaignsModule } from './pages/campaigns/campaigns.module';
import { CreateNewAutomationModule } from './pages/create-new-automation/create-new-automation.module';
import { CreateNewEmailTemplateModule } from './pages/create-new-email-template/create-new-email-template.module';
import { CreateNewPortalMessageModule } from './pages/create-new-portal-message/create-new-portal-message.module';
import { CreateNewTemplateModule } from './pages/create-new-template/create-new-template.module';
import { DefaultModule } from './pages/default/default.module';
import { EditEmailTemplateModule } from './pages/edit-email-template/edit-email-template.module';
import { NotificationsModule } from './pages/notifications/notifications.module';
import { PagesModule } from './pages/pages.module';
import { ReportsModule } from './pages/reports/reports.module';
import { SentNotificationModule } from './pages/sent-notification/sent-notification.module';
import { TemplateBuilderModule } from './pages/template-builder/template-builder.module';
import { TemplatesModule } from './pages/templates/templates.module';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [AutomatedNotificationRoutingModule,
  EditEmailTemplateModule,
  PagesModule,
  TemplateBuilderModule,
  CreateNewAutomationModule,
  NotificationsModule,
  DefaultModule,
  CreateNewTemplateModule,
  CreateNewEmailTemplateModule,
  CreateNewPortalMessageModule,
  ReportsModule,
  TemplatesModule,
  AudienceModule,
  AutomateModule,
  SentNotificationModule,
  CampaignsModule
];

const COMPONENTS = [AutomatedNotificationComponent];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []

})
export class AutomatedNotificationModule { }
