import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'tei-automated-notification-sent-menu',
  templateUrl: './sent-notification-menu.component.html',
  styleUrls: ['./sent-notification-menu.component.scss']
})
export class SentNotificationMenuComponent implements OnInit
{
  @Input() title = '';
  @Input() model = [];
  @Input() x: number;
  @Output() clickedItem = new EventEmitter<string>();

  clickHandler(item: string)
  {
    this.clickedItem.emit(item);
  }
  constructor() { }

  ngOnInit(): void
  {
  }

  get listOfEmails(): string[]
  {
    return this.model.filter(f => f !== this.model[0]);
  }

}
