import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetCreateNewEmailTemplateResponse } from './get-create-new-email-template.model';
import { GetEmailTemplateResponse, PostEmailTemplateRequestFormArray, ModuleTypeTemplate } from '../../email-template';

export class PostCreateNewEmailTemplateRequest
{
  
  EmailTemplate: GetEmailTemplateResponse;
}

export class PostCreateNewEmailTemplateRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      //EmailMessageTemplate: new PostEmailTemplateRequestFormArray(automationResponse),
      EmailTemplate: new UntypedFormControl()
    });
  }

  public value: PostCreateNewEmailTemplateRequest;

  public setValue(value: GetCreateNewEmailTemplateResponse)
  {
    let emailTemplate = new ModuleTypeTemplate();
    
    const request = new PostCreateNewEmailTemplateRequest();
    request.EmailTemplate = value.EmailTemplate;
    //emailTemplate = value.EmailTemplate.Templates.find(x=>x.TemplateID == value.EmailTemplate.TemplateID);
  
    super.setValue(request);
   
  }
}

export class PostCreateNewEmailTemplateResponse
{
  public EmailTemplate: GetEmailTemplateResponse;
}
