import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyListOption as MatListOption } from '@angular/material/legacy-list';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { MatSort } from '@angular/material/sort';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, ConfirmDialogService, DialogData } from '@tei/material';
import { GetTemplatesRequest, GetTemplatesResponse, ITemplatesService, PostTemplatesRequestForm, Template, TemplateMetadata } from '../../@intermediate/templates';
import { DeleteTemplatesRequest } from '../../@intermediate/templates/models/delete-templates.model';
import { DuplicateTemplatesRequest } from '../../@intermediate/templates/models/duplicate-templates.model';
import { TemplateFilterService } from '../../@services/templatefilter.service';
import { TemplatesFiltersDialogComponent } from '../../components/templates-filters-dialog/templates-filters-dialog.component';
import { TemplateMatDialogComponent } from '../template-mat-dialog/template-mat-dialog.component';


@Component({
  selector: 'tei-automated-notification-pages-templates',
  templateUrl: './templates.component.html',
  styleUrls: ['./templates.component.scss']
})
export class TemplatesComponent implements OnInit
{
  model: GetTemplatesResponse;
  form: PostTemplatesRequestForm;
  metadata = new TemplateMetadata();
  datasource: MatTableDataSource<Template>;
  automationHeader: string = "Automation";
  @ViewChild('allSelected') private allSelected: MatListOption;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.MARKETING'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.NOTIFICATIONS.NOTIFICATIONS'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.TEMPLATES.EMAIL_TEMPLATES'),
      relativeUrl: ''
    }
  ];

  constructor(
    @Inject('ITemplatesService') private TemplatesService: ITemplatesService,
    public dialog: MatDialog,
    private confirmdialogService: ConfirmDialogService,
    private translate: TranslateService,
    private router: Router,
    private templateFilterService: TemplateFilterService
  ) { }

  ngOnInit()
  {

    this.form = new PostTemplatesRequestForm();
    const request = new GetTemplatesRequest();
    this.load(request);
    const filteredValue = this.templateFilterService.currentFilter.subscribe(filterValue =>
    {
      this.filterFromDialog(filterValue);
    });
  }

  private load(request: GetTemplatesRequest)
  {
    if (!this.model)
    {
      this.model = new GetTemplatesResponse();
    }

    this.TemplatesService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.bindAutomations(this.model.Templates);

    });
  }


  bindAutomations(templates: Template[])
  {
    this.datasource = new MatTableDataSource<Template>(templates.filter(x => x.Selected == true));
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }

  filterFromDialog(filterValue)
  {
    if (filterValue != null)
    {
      this.filterTemplates(filterValue);
    }
  }



  filterTemplates(value)
  {

    let searchString = value.SearchString;
    if (this.model.Templates)
    {
      this.model.Templates.forEach(x => { x.Selected = false; });

      value.AutomationModule.forEach(e =>
      {
        if (e == 0)
        {
          this.model.Templates.filter(f => f.TemplateCategoryID == 0).forEach(f => { f.Selected = true; });
        }
        this.model.Templates.filter(f => f.ModuleID == e.ModuleID && e.IsEnabled == true).forEach(f => { f.Selected = true; });
      });
      let templates = this.model.Templates.filter(c => c.Name.toLowerCase().includes(searchString.toLowerCase()));
      this.bindAutomations(templates);
    }
  }



  openDialog(): void
  {
    let dialogRef = this.dialog.open(TemplatesFiltersDialogComponent, {
      panelClass: '',
      height: '100vh',
      width: '100vh',
      maxWidth: '100vh',
      data: { template: true }
    });

    dialogRef.afterClosed().subscribe(value =>
    {
    });
  }

  createTemplate()
  {

    const dialogRef = this.dialog.open(TemplateMatDialogComponent, {
      maxWidth: "700px",
      width: "80%",
      data: {
        automateTypes: { AutomatedModuleFilters: this.model.AutomationModules, Templates: this.model.Templates }
      }
    });
    dialogRef.afterClosed().subscribe(formNewAutomation =>
    {
    });
  }


  deleteTemplate(templateID: string)
  {
    // index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    // const template = this.model.Templates[index];
    const index = this.model.Templates.findIndex(f => f.TemplateID == templateID);
    const template = this.model.Templates.filter(f => f.TemplateID == templateID)[0];

    const dialogData: DialogData = {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DELETE'),
      message: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DELETE_MESSAGE', { name: template.Name }),
      textOkayButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DELETE'),
      textCancelButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.CANCEL'),
      buttonColor: 'warn'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DeleteTemplatesRequest();
        request.TemplateID = template.TemplateID;

        this.TemplatesService.Delete(request).subscribe(response =>
        {
          this.model.Templates.splice(index, 1);
          this.bindAutomations(this.model.Templates);
        });
      }
    });

  }

  duplicateTemplate(templateID: string)
  {

    // index = (this.paginator.pageIndex * this.paginator.pageSize) + index;
    // const template = this.model.Templates[index];

    const index = this.model.Templates.findIndex(f => f.TemplateID == templateID);
    const template = this.model.Templates.filter(f => f.TemplateID == templateID)[0];

    const dialogData: DialogData = {
      title: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DUPLICATE'),
      message: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DUPLICATE_MESSAGE', { name: template.Name }),
      textOkayButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.DUPLICATE'),
      textCancelButton: this.translate.instant('AUTOMATED_NOTIFICATION.AUTOMATE.CANCEL'),
      buttonColor: 'accent'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DuplicateTemplatesRequest();
        request.TemplateID = template.TemplateID;

        this.TemplatesService.Duplicate(request).subscribe(response =>
        {
          this.router.navigate(['/office/marketing/notifications/create-new-template/duplicate', response.TemplateID, response.TemplateCategoryID, response.AutomationTypeID]);
        });
      }
    });

  }
}
