export class GetSentNotificationRequest
{
  Type: string;
  FromDate: string;
  ToDate: string;
  From: string;
  To: string;
  Filter?: string;
  SortOrder: string;
  SortType: SortType;
  PageNumber: number;
  PageCount: number;
}

export enum SortType
{
  Ascending = 0,
  Descending = 1
}

export class GetSentNotificationResponse
{
  Notifications: Notification[];
  TotalCount: number;
}

export class Notification
{
  NotificationID: number;
  Type: string;
  ReferenceID: string;
  Subject: string;
  From: string;
  To: Array<string>;
  ConfirmEmail: string;
  Date: Date;
  Status: string;
  SentOn?: Date;
  // this is calculated of To[0]
  Receiver: string;
}
