import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, SnackBarModule } from '@tei/material';
import { CreateNewAutomationService } from '../../@services/create-new-automation.service';
import { FollowUpModule } from '../../components/follow-up/follow-up.module';
import { PortalMessageTemplateModule } from '../../components/portal-message-template/portal-message-template.module';
import { ReminderModule } from '../../components/reminder/reminder.module';
import { CreateNewAutomationComponent } from './create-new-automation.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatDividerModule, MatListModule, MatCheckboxModule, MatCardModule, MatButtonModule, MatDialogModule, MatInputModule, MatIconModule, MatExpansionModule, MatChipsModule];
const TEI_MATERIAL_MODULES = [PortalMessageTemplateModule, SnackBarModule, ReminderModule, FollowUpModule, BreadCrumbModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];

@NgModule({
  declarations: [CreateNewAutomationComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: CreateNewAutomationComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'ICreateNewAutomationService', useClass: CreateNewAutomationService }],
})
export class CreateNewAutomationModule { }
