import { Location } from '@angular/common';
import { Component, EventEmitter, Inject, Input, OnInit, Output, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { DynamicFormService } from '@tei/material-generic-filter';
import { FieldConfig } from '@tei/material-generic-filter/lib/field.interface';
import { AutomationModule } from '../../@intermediate/templates';
import { GetTemplatesFiltersDialogRequest, GetTemplatesFiltersDialogResponse, ITemplatesFiltersDialogService, PostTemplatesFiltersDialogRequestForm } from '../../@intermediate/templates-filters-dialog';
import { TemplateFilterService } from '../../@services/templatefilter.service';



@Component({
  selector: 'tei-automated-notification-templates-filters-dialog',
  templateUrl: './templates-filters-dialog.component.html',
  styleUrls: ['./templates-filters-dialog.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(TemplatesFiltersDialogComponent)
  ]
})
export class TemplatesFiltersDialogComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetTemplatesFiltersDialogResponse;
  form: PostTemplatesFiltersDialogRequestForm;
  @Input() AutomationHeaderName: string;
  @Input() CampaignEnabled: boolean;
  @Input() view: string = 'md';
  @Output()
  filterEmitter = new EventEmitter<any>();
  regConfig: FieldConfig[] = [

    {
      type: "input",
      name: "SearchString",
      label: "Search by Name",
      inputType: "text",
      isSearchText: true,
    },
    {
      type: "selectionlist",
      name: "AutomationModule",
      needAll: true,
      label: "Select All"
    },


  ];

  selectedFilters: string[] = [];
  showFilter = false;
  ifAllSelected = false;


  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('ITemplatesFiltersDialogService') private TemplatesFiltersDialogService: ITemplatesFiltersDialogService,
    private dialogRef: MatDialog,
    public dialogRefData: MatDialogRef<TemplatesFiltersDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private location: Location,
    private teamplateFiletr: TemplateFilterService,
    private dynamicfilter: DynamicFormService)
  {
    super(_errorStateMatcher);
  }



  ngOnInit()
  {

    this.getURLFiltersParam();
    this.form = new PostTemplatesFiltersDialogRequestForm();
    const request = new GetTemplatesFiltersDialogRequest();
    this.load(request);
    this.form.valueChanges.subscribe(
      value =>
      {
        this.filterEmitter.emit(value);
      }
    );

    if (this.data.template == true || this.CampaignEnabled == true)
    {
      this.CampaignEnabled = true;
      this.regConfig[1].options = [];
      this.regConfig[1].value = [];
      if (this.CampaignEnabled)
      {
        if (this.ifAllSelected || this.selectedFilters.includes('-1'))
        {
          this.regConfig[1].value.push('-1');
        }
        this.regConfig[1].options.push(
          {
            display: "Campaign",
            value: "-1"
          });
      }

    }
    else
    {
      this.regConfig[1].options = [];
      this.regConfig[1].value = [];
      this.CampaignEnabled = false;
    }

    if (this.ifAllSelected)
    {
      this.regConfig[1].value.push(0);
    }
  }

  getURLFiltersParam(): void
  {
    const params = this.activatedRoute.snapshot.queryParamMap;
    if (params.has("filter"))
    {
      this.selectedFilters = params.get("filter").split(',');
      this.ifAllSelected = this.selectedFilters.length === 1 && this.selectedFilters.includes('0');
    } else
    {
      this.generateFilterURL(['0']);
      this.ifAllSelected = true;
    }
  }


  private load(request: GetTemplatesFiltersDialogRequest)
  {
    if (!this.model)
    {
      this.model = new GetTemplatesFiltersDialogResponse();
    }
    let formUpdated = false;
    this.TemplatesFiltersDialogService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.model.AutomationModules = this.model.AutomationModules.map(m => ({
        ...m,
        IsEnabled: this.ifAllSelected || this.selectedFilters.includes(m.ModuleID.toString())
      }));
      this.form.setValue(this.model);
      formUpdated = true;
      this.model.AutomationModules.forEach(x =>
      {
        if (this.ifAllSelected || this.selectedFilters.includes(x.ModuleID.toString()))
        {
          this.regConfig[1].value.push(x.ModuleID.toString());
        }
        this.regConfig[1].options.push(
          {
            display: x.ModuleName,
            value: x.ModuleID.toString(),
          });
      });


      this.dynamicfilter.currentFilter.subscribe(filterValue =>
      {
        if (formUpdated)
        {
          filterValue = null;
        }

        if (filterValue)
        {
          this.generateFilterURL(filterValue.AutomationModule as string[]);
          var tempCampaign: AutomationModule;
          tempCampaign = new AutomationModule();
          this.model.AutomationModules.forEach(z =>
          {
            z.IsEnabled = filterValue.AutomationModule.filter(x => x == z.ModuleID.toString()).length > 0;
          });

          filterValue.AutomationModule.filter(f =>
          {
            if (f == "campaign")
            {
              var camp = this.model.AutomationModules.filter(f => f.ModuleName == "Campaign");
              tempCampaign.IsEnabled = true;
              tempCampaign.ModuleName = "Campaign";
              tempCampaign.ModuleID = -1;
              this.model.AutomationModules.push(tempCampaign);
            }
          });

          this.form.get("SearchString").patchValue(filterValue.SearchString ? filterValue.SearchString : "");
          this.form.get("AutomationModule").patchValue(this.model.AutomationModules);
          this.form.patchValue(this.model);

        }
        else
        {
          tempCampaign = new AutomationModule();
          tempCampaign.IsEnabled = this.ifAllSelected || this.selectedFilters.includes("-1");
          tempCampaign.ModuleName = "Campaign";
          tempCampaign.ModuleID = -1;
          this.model.AutomationModules.push(tempCampaign);
          this.form.get("AutomationModule").patchValue(this.model.AutomationModules);
          this.form.patchValue(this.model);
        }
        formUpdated = false;
      });
      this.showFilter = true;
    });
  }

  ApplyFilter(): void
  {
    this.teamplateFiletr.setValue(this.form.value);
    this.dialogRef.closeAll();
  }

  generateFilterURL(keywords: string[]): void
  {
    const keywordsToString = keywords.map(m => m.toString());
    const url = this.router.createUrlTree([], { relativeTo: this.activatedRoute, queryParams: { filter: keywordsToString.includes('0') ? '0' : keywordsToString.join(',') } }).toString();
    this.location.go(url);
  }

}
