import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { GetAutomateRequest, GetAutomateResponse } from '../@intermediate/automate';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class SentNotificationHeaderService {
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient) {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Automate';
  }

  public Get(request: GetAutomateRequest): Observable<GetAutomateResponse> {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAutomateResponse>(apiUrl, { params: request as any });
  }
}
