import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable, of } from 'rxjs';
import { Campaign, GetCampaignsRequest, GetCampaignsResponse, ICampaignsService } from '../@intermediate/campaigns';

@Injectable({
  providedIn: 'root'
})

export class CampaignsService implements ICampaignsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Campaigns';
  }

  public Get(request: GetCampaignsRequest): Observable<GetCampaignsResponse>
  {
    const response = new GetCampaignsResponse();
    response.Campaigns = [];
    response.TotalCount = 50;
    for (let i = 0; i < 50; i++)
    {
      const campaign = new Campaign();
      campaign.Clicked = 100 + i;
      campaign.Date = new Date();
      campaign.Name = 'Campagin ' + i;
      campaign.Recipients = 100 + i;
      campaign.Views = 100 + i;
      campaign.Status = Math.floor(Math.random() * 3) + 0;
      response.Campaigns.push(campaign);
    }
    return of(response);

    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCampaignsResponse>(apiUrl, { params: request as any });
  }
}
