import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCreateNewAutomationRequest, GetCreateNewAutomationResponse, ICreateNewAutomationService, PostCreateNewAutomationRequest, PostCreateNewAutomationResponse } from '../@intermediate/create-new-automation';

@Injectable({
  providedIn: 'root'
})

export class CreateNewAutomationService implements ICreateNewAutomationService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/CreateNewAutomation';
  }

  public Get(request: GetCreateNewAutomationRequest): Observable<GetCreateNewAutomationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCreateNewAutomationResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCreateNewAutomationRequest): Observable<PostCreateNewAutomationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateNewAutomationResponse>(apiUrl, request);
  }
}
