import { Component, OnInit, Inject } from '@angular/core';
import { IReportsService, GetReportsRequest, GetReportsResponse } from '../../@intermediate/reports';

@Component({
  selector: 'tei-automated-notification-pages-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.scss']
})
export class ReportsComponent implements OnInit
{
  model: GetReportsResponse;

  constructor(@Inject('IReportsService') private ReportsService: IReportsService)
  {
    
  }

  ngOnInit()
  {
    const request = new GetReportsRequest();
    this.load(request);
  }

  private load(request: GetReportsRequest)
  {
    if (!this.model)
    {
      this.model = new GetReportsResponse();
    }

    this.ReportsService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }
}
