import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetDefaultRequest, GetDefaultResponse, IDefaultService } from '../@intermediate/default';

@Injectable({
  providedIn: 'root'
})

export class DefaultService implements IDefaultService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/AutomatedNotification/Default';
  }

  public Get(request: GetDefaultRequest): Observable<GetDefaultResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDefaultResponse>(apiUrl, { params: request as any });
  }
}
